import React, { memo, useState } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { dataLayer } from '@weatherbug/common';
import { FeedbackFormView } from './FeedbackFormView';
import { validateEmail } from '../../common/utilities/email';

import { getAppId, getAppVersion } from '../../modules/main/AppRegistration';

import {
  getStandardApiConfig,
  type StandardApiConfigT,
} from '../../common/config/getStandardApiConfig';
import { AppStateManager } from '../../common/utilities/appStateManager';
const { sendFeedback } = dataLayer.actions;

export const FeedbackForm = memo(({ setFeedbackFormOpen }) => {
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [comments, setComments] = useState('');
  const [userEmail, setEmail] = useState('');
  const [userName, setName] = useState('');
  const [errorMessage, setErrorMessage] = useState(undefined);
  const [isValid, setValid] = useState(true);
  const dispatch = useDispatch();

  const { location, appInstanceId, station } = useSelector((state) => ({
    location: state.locationDetails?.locationDetails,
    appInstanceId: state.entityManagement.appInstanceId,
    station: state.currentStationObs?.currentStationObs,
  }));

  const handleEmailChange = (event) => {
    event.preventDefault();
    setEmail(event.target.value);
  };

  const handleNameChange = (event) => {
    event.preventDefault();
    setName(event.target.value);
  };

  const handleMessageChange = (event) => {
    event.preventDefault();
    setComments(event.target.value);
  };

  //TECHDEBT: Move to constants file when one exists
  const pushToken = '0000000000000000000000000000000000000000000000000000000011111111';

  const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
  const { authId, secret, stdParameters } = apiConfig;

  const validateForm = () => {
    let error = undefined;
    let valid = true;
    if (!userEmail) {
      error = 'Email is required';
      valid = false;
    }
    if (!validateEmail(userEmail)) {
      error = 'Invalid Email Address';
      valid = false;
    }
    if (!userName) {
      valid = false;
    }
    if (!userEmail) {
      valid = false;
    }
    if (!comments) {
      valid = false;
    }
    setValid(valid);
    setErrorMessage(error);
    return valid;
  };

  const handleCloseButton = (event) => {
    event.preventDefault();
    setIsSubmitted(false);
    setErrorMessage(undefined);
    setEmail(undefined);
    setName(undefined);
    setComments(undefined);
    setFeedbackFormOpen();
    setValid(true);
  };

  const handleFormSubmit = (formSubmitEvent) => {
    formSubmitEvent.preventDefault();

    if (!validateForm()) {
      return;
    } else {
      if (appInstanceId && station && apiConfig) {
        const feedbackBody = {
          User: {
            RawFeedbackMessage: comments,
            EmailAddress: userEmail,
            AppInstanceId: appInstanceId,
            CultureInfo: 'en-us',
            Name: userName,
            SignInId: userEmail,
          },
          App: {
            Id: getAppId(),
            Version: getAppVersion(),
            Name: 'WeatherBug Browser Extension',
          },
          Os: {
            Type: AppStateManager._getOs(),
            Version: AppStateManager._getOsVersion(),
          },
          Device: {},
          Legal: {
            PrivacyPolicyVersion: 'PP-US-EN-20180523-00',
            TermsOfUsageVersion: 'TOU-US-EN-20180523-00',
          },
          Location: {
            Selected: {
              Coordinates: {
                Latitude: location?.latitude,
                Longitude: location?.longitude,
              },
              City: location?.cityName,
              StateProvidence: location?.territoryName,
              Country: location?.countryIso2Code,
              Station: {
                ProviderId: station?.station?.providerId,
                StationId: station?.station?.stationId,
                StationName: station?.station?.stationName,
              },
              LocationName: `${location?.cityName}, ${location?.territoryName}`,
            },
          },
          Push: {
            PushToken: pushToken,
          },
        };

        dispatch(
          sendFeedback(apiConfig?.FEEDBACK_URL, authId, secret, feedbackBody, stdParameters, true),
        );
        setIsSubmitted(true);
      }
    }
  };

  return (
    <FeedbackFormView
      isSubmitted={isSubmitted}
      handleEmailChange={handleEmailChange}
      handleNameChange={handleNameChange}
      handleMessageChange={handleMessageChange}
      handleFormSubmit={handleFormSubmit}
      isValid={isValid}
      userName={userName}
      userEmail={userEmail}
      comments={comments}
      handleCloseButton={handleCloseButton}
      errorMessage={errorMessage}
    />
  );
});

FeedbackForm.displayName = 'FeedbackForm';

FeedbackForm.propTypes = {
  setFeedbackFormOpen: PropTypes.func,
};
