/* @flow */
import styled, { css } from 'styled-components';

export const Title1 = styled.h1`
  font-size: 64px;
  font-weight: 300;
  color: #ffffff;
  margin: 0;
`;

export const Title2 = styled.h2`
  font-size: 28px;
  font-weight: 400;
  color: #ffffff;
  margin: 0;
`;

export const Title3 = styled.h4`
  font-size: 24px;
  font-weight: 400;
  color: #ffffff;
  margin: 0;
`;

const BaseText = styled.p`
  color: white;
  margin: 0;
  ${props =>
    'lineClamp' in props &&
    css`
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: ${props.lineClamp};
      overflow: hidden;
    `}
`;

export const Text1 = styled(BaseText)`
  font-size: 26px;
  font-weight: 300;
`;

// bold, semiBold
export const Text2 = styled(BaseText).attrs(props => {
  const weight = (function() {
    if (props.bold) {
      return '700';
    } else if (props.semiBold) {
      return '600';
    } else {
      return '400';
    }
  })();

  return {
    weight,
  };
})`
  font-size: 18px;
  font-weight: ${props => props.weight};
`;

export const Text3 = styled(BaseText).attrs(props => {
  const color = (function() {
    if (props.emphasize) {
      return 'white';
    } else {
      return 'rgba(255, 255, 255, 0.6)';
    }
  })();

  return {
    color,
  };
})`
  font-size: 14px;
  font-weight: 300;
  color: ${props => props.color};
`;
