// @flow

import { GET_APP_INSTANCE, UPDATE_CUSTOM_USER_SETTINGS } from '../actions/entityManagementActions';

type ReduxAction = Object;

export type State = {
  appInstanceId: ?string,
  userSettings: mixed,
};

const initialState: State = {
  appInstanceId: undefined,
};

export function entityManagement(state: State = initialState, action: ReduxAction): State {
  switch (action.type) {
    case GET_APP_INSTANCE.fulfilled: {
      return {
        ...state,
        appInstanceId: action.payload.appInstanceId,
      };
    }
    case UPDATE_CUSTOM_USER_SETTINGS.fulfilled: {
      return {
        ...state,
        userSettings: action.payload,
      };
    }

    default: {
      return state;
    }
  }
}
