/* @flow */
import { dataLayer } from '@weatherbug/common';
import { getStandardApiConfig, type StandardApiConfigT } from '../config/getStandardApiConfig';

export function getComplianceInfo(latitude, longitude) {
  return dispatch => {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { LEGAL_API_URL, authId, secret, stdParameters } = apiConfig;

    return dispatch(
      dataLayer.actions.getComplianceInfo(
        LEGAL_API_URL,
        authId,
        secret,
        undefined,
        undefined,
        stdParameters,
        false,
        true,
        'LatitudeLongitude',
        ` ${latitude},${longitude}`,
      ),
    );
  };
}
