/* @flow */

import { GET_CONFIG_SETTING } from '../actions/remoteConfigActions';
import localAppConfig from '../resources/appConfig';

export type State = {
  isloaded: boolean,
  config: typeof localAppConfig,
};

const initialState: State = {
  isloaded: false,
  config: localAppConfig,
};

export default (state: State = initialState, action: Object): State => {
  switch (action.type) {
    case GET_CONFIG_SETTING.pending: {
      return {
        ...state,
        isloaded: false,
      };
    }

    case GET_CONFIG_SETTING.fulfilled: {
      return {
        ...state,
        isloaded: true,
        config: {
          ...state.config,
          ...action.payload,
        },
      };
    }

    case GET_CONFIG_SETTING.rejected: {
      return {
        ...state,
        isloaded: true,
      };
    }

    default:
      return state;
  }
};
