/* @flow */

import React, { memo, useState } from 'react';
import styled from 'styled-components';
import Measure from 'react-measure';
import { TaboolaAd } from './TaboolaAd';
import LocalStorage from '../shared/LocalStorage';
import { useSelector } from 'react-redux';

type Props = {
  className?: string,
};
export const TaboolaView = styled(
  memo(
    ({
      className,
      shouldRenderAdView = false,
      modeId,
      containerId,
      placementId,
      targetType,
      divId,
      adHeight,
      adWidth,
      isLastSnippet,
      ccpa,
      shouldSpan,
    }: Props) => {
      const [ccpaClicked, setCcpaClicked] = useState(false);
      useSelector((store) => {
        if (store && store.ccpaReducer.ccpaOptStatus) {
          if (ccpaClicked !== store.ccpaReducer.ccpaOptStatus) {
            setCcpaClicked(!ccpaClicked);
          }
        }
      });
      let cdnsStatus = 'none';
      if (ccpa) {
        cdnsStatus = LocalStorage.getCCPAOptOutStatus() ? 'true' : 'false';
      }
      return (
        <div className={className}>
          {shouldRenderAdView && (
            <Measure>
              {({ measureRef, contentRect }) => {
                return (
                  <Container ref={measureRef}>
                    <Content>
                      <div style={{ padding: shouldSpan ? 34 : 0 }}>
                        <TaboolaAd
                          modeId={modeId} //'thumbnails-a'
                          containerId={containerId} //'taboola-below-content-thumbnails---now-forecast'
                          placementId={placementId} //'Below Content Thumbnails - Now Forecast'
                          targetType={targetType} //'mix'
                          divId={divId} //'taboola-below-content-thumbnails---now-forecast'
                          isLastSnippet={isLastSnippet} //true
                          shouldRenderAdView={shouldRenderAdView}
                          cdns={cdnsStatus}
                        />
                      </div>
                    </Content>
                  </Container>
                );
              }}
            </Measure>
          )}
        </div>
      );
    },
  ),
)``;

const Container = styled.div`
  ${TaboolaAd} {
    margin: 0 auto;
  }
`;
const Content = styled.div`
  padding: 8px;
`;
