// @flow
import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import { Storage } from '@weatherbug/common';

import configureStore from './common/config/store';
import { getConfigSetting } from './common/actions/remoteConfigActions';
import './App.css';

import { MainView } from './modules/main/MainView';

const store = configureStore();

const storage = new Storage({
  // maximum capacity, default 1000
  size: 1000,
  // Use AsyncStorage for RN, or window.localStorage for web. If not set, data would be lost after reload.
  storageBackend: window.localStorage,
  //30 minutes
  defaultExpires: 1000 * 900,
  // cache data in the memory. default is true.
  enableCache: true,
});
window.storage = storage;

class App extends Component<{}> {
  loadConfig() {
    const url = process.env.REACT_APP_CONFIG_URL;
    if (!url) {
      console.error('Missing REACT_APP_CONFIG_URL');
      return;
    }
    store.dispatch(getConfigSetting(url, false));
  }

  componentDidMount() {
    this.loadConfig();
  }

  render() {
    return (
      <Provider store={store}>
        <Router>
          <div className="App">
            <Switch>
              <Route path="/" component={MainView} />
            </Switch>
          </div>
        </Router>
      </Provider>
    );
  }
}

export default App;
