// @flow
import { dataLayer, utilities } from '@weatherbug/common';
import { GET_MY_LOCATION_BY_IP } from '../actions/locationSearchActions';

const { GET_LOCATION_DETAILS_BY_LATLON, GET_LOCATION_DETAILS_BY_SLUG } = dataLayer.actions;

const { transformToCamelCase } = utilities;

// TODO move this to @weatherbug/common

type ReduxAction = Object;

type State = {
  locationDetailsIsRefreshing: boolean,
  userDefaultLocationIsRefreshing: boolean,
  lastError: ?Object,
  locationDetails: ?{},
  userDefaultLocation: ?{},
};

const initialState: State = {
  locationDetailsIsRefreshing: false,
  userDefaultLocationIsRefreshing: false,
  lastError: undefined,
  locationDetails: undefined,
  userDefaultLocation: undefined,
};

export function locationDetails(state: State = initialState, action: ReduxAction): State {
  switch (action.type) {
    case GET_MY_LOCATION_BY_IP.pending: {
      return {
        ...state,
        userDefaultLocationIsRefreshing: true,
      };
    }

    case GET_MY_LOCATION_BY_IP.fulfilled: {
      const newState = {
        ...state,
        userDefaultLocation: action.payload,
        lastError: {},
        userDefaultLocationIsRefreshing: false,
      };
      return newState;
    }

    case GET_MY_LOCATION_BY_IP.rejected: {
      return {
        ...state,
        // locationDetails: undefined,
        lastError: action.payload,
        userDefaultLocationIsRefreshing: false,
      };
    }

    case GET_LOCATION_DETAILS_BY_LATLON.pending: {
      return {
        ...state,
        locationDetailsIsRefreshing: true,
      };
    }

    case GET_LOCATION_DETAILS_BY_LATLON.fulfilled: {
      transformToCamelCase(action.payload);
      return {
        ...state,
        locationDetails: action.payload,
        lastError: {},
        locationDetailsIsRefreshing: false,
      };
    }

    case GET_LOCATION_DETAILS_BY_LATLON.rejected: {
      return {
        ...state,
        locationDetails: undefined,
        lastError: action.payload,
        locationDetailsIsRefreshing: false,
      };
    }

    case GET_LOCATION_DETAILS_BY_SLUG.pending: {
      return {
        ...state,
        locationDetailsIsRefreshing: true,
      };
    }

    case GET_LOCATION_DETAILS_BY_SLUG.fulfilled: {
      return {
        ...state,
        locationDetails: action.payload,
        lastError: {},
        locationDetailsIsRefreshing: false,
      };
    }

    case GET_LOCATION_DETAILS_BY_SLUG.rejected: {
      return {
        ...state,
        locationDetails: undefined,
        lastError: action.payload,
        locationDetailsIsRefreshing: false,
      };
    }
    default: {
      return dataLayer.reducers.locationDetails(state, action);
    }
  }
}
