// @flow
import { combineReducers } from 'redux';
import { dataLayer } from '@weatherbug/common';
import { dataLayer as webAdsDL } from '@weatherbug/web-ads';

import { locationDetails } from './locationDetailsReducer';
import { locationSearch } from './locationSearchReducer';
import { userConfig } from './userConfigReducer';
import { entityManagement } from './entityManagementReducer';
import remoteConfig from './remoteConfigReducer';
import ccpaReducer from './ccpaReducer';

import type { State as EntityManagementState } from './entityManagementReducer';
import type { State as RemoteConfigState } from './remoteConfigReducer';

const rootReducer = combineReducers({
  ...dataLayer.reducers,
  ...webAdsDL.reducers,
  locationDetails,
  locationSearch,
  userConfig,
  remoteConfig,
  entityManagement,
  ccpaReducer,
});
export default rootReducer;

export type ReduxState = {
  entityManagement: EntityManagementState,
  remoteConfig: RemoteConfigState,
};
