// @flow
import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { debounce } from 'lodash';
import { bindActionCreators } from 'redux';

import { Icon } from '../shared/Icon';
import { loadDataForLatLong } from '../../common/actions/appActions';
import { SearchBar } from '../shared/SearchBar';
import LocalStorage from '../shared/LocalStorage';

import {
  searchLocationBySearchString,
  locationSearchTextChanged,
  searchLocationSelected,
} from '../../common/actions/locationSearchActions';
import type { State as LocationSearchState } from '../../common/reducers/locationSearchReducer';

type Props = {
  locationSearchResults: $PropertyType<LocationSearchState, 'locationSearchResults'>,
  locationSearchIsQuerying: $PropertyType<LocationSearchState, 'locationSearchIsQuerying'>,
  searchText: $PropertyType<LocationSearchState, 'searchText'>,
  searchLocationSelected: typeof searchLocationSelected,
  locationSearchTextChanged: typeof locationSearchTextChanged,
  searchLocationBySearchString: typeof searchLocationBySearchString,
  locationName: ?string,
};

type State = {
  isFocused: boolean,
  overrideLocationName: string,
};
class LocationSearchBare extends React.Component<Props, State> {
  debouncedSearch = (text: string) => undefined;

  constructor(props) {
    super(props);

    this.state = {
      isFocused: false,
      overrideLocationName: '',
    };

    this.debouncedSearch = debounce(
      (text: string) => {
        this.runSearch(text);
      },
      250,
      { leading: false, trailing: true },
    );
  }

  runSearch = (text: string) => {
    this.props.searchLocationBySearchString({ searchText: text });
  };

  render() {
    const { locationName, locationSearchIsQuerying, locationSearchResults } = this.props;
    const { isFocused, overrideLocationName } = this.state;
    const placeholder = isFocused ? 'Search City or Zip' : overrideLocationName || locationName;
    return (
      <SearchBar
        icon={
          <IconContainer>
            <Icon name="Search" size={18} color="#888888" />
          </IconContainer>
        }
        placeholder={placeholder}
        onChangeFocus={isFocused => {
          this.setState({ isFocused });
        }}
        isSearching={locationSearchIsQuerying}
        searchResults={locationSearchResults.map(entry => {
          return (entry && entry.DisplayCompositeName) || '';
        })}
        onChangeSearchTerm={searchTerm => {
          // do search with newTerm to update locations
          this.debouncedSearch(searchTerm);
        }}
        onSelectResult={(result, index) => {
          const location = locationSearchResults[index];
          const { Latitude: latitude, Longitude: longitude } = location;
          this.props.searchLocationSelected(location);
          LocalStorage.setUserSelectedLocation(location);
          // This immediately updates the location name in the bar, without having to wait for the api response.
          this.setState({
            isFocused: false,
            overrideLocationName: (location && location.displayCompositeName) || '',
          });
          this.props.loadDataForLatLong(latitude, longitude).then(resp => {
            this.setState({ overrideLocationName: '' });
          });
        }}
      />
    );
  }
}

const select = store => {
  const {
    locationSearch: { locationSearchResults, locationSearchIsQuerying, searchText },
    locationDetails: { locationDetails },
  } = store;
  return {
    locationSearchResults: locationSearchResults.slice(0, 5),
    locationSearchIsQuerying,
    searchText,
    locationName: locationDetails && locationDetails.displayCompositeName,
  };
};

function mapActions(dispatch) {
  return bindActionCreators(
    {
      searchLocationBySearchString,
      loadDataForLatLong,
      searchLocationSelected,
    },
    dispatch,
  );
}

export const LocationSearch = connect(
  select,
  mapActions,
)(LocationSearchBare);

const IconContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  &:hover {
    cursor: pointer;
  }
`;
