// @flow

import { dataLayer } from '@weatherbug/common';

import {
  getStandardApiConfig,
  type StandardApiConfigT,
} from '../../common/config/getStandardApiConfig';

const DEFAULT_VERBOSE = true;

type float = number;

export type LocationDetailsPayload = {
  CityId: string,
  CityName: string,
  Country: string, // "US"
  Dma: string,
  Latitude: float,
  Longitude: float,
  SlugName: string,
  StateCode: null,
  Territory: null,
  Zip: string,
  responseTime: number,
};

export function getLocationDetailsByLatLon({
  latitude,
  longitude,
  offset = 0,
  units = 'english',
  verbose = DEFAULT_VERBOSE,
  refresh = false,
}: {
  latitude: number,
  longitude: number,
  offset?: number,
  units?: 'english' | 'metric',
  verbose?: boolean,
  refresh?: boolean,
}) {
  return dispatch => {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { LOCATIONS_API_URL, authId, secret, stdParameters } = apiConfig;
    return dispatch(
      dataLayer.actions.getLocationDetailsByLatLon(
        latitude,
        longitude,
        LOCATIONS_API_URL,
        authId,
        secret,
        stdParameters,
        verbose,
        refresh,
      ),
    );
  };
}
