/* @flow */
import { baseClasses } from '@weatherbug/common';
import RemoteConfigProvider from '../providers/RemoteConfigProvider';

const { AsyncAction } = baseClasses;

export const GET_CONFIG_SETTING = new AsyncAction('GET_CONFIG_SETTING');

export function getConfigSetting(url: string, refresh: boolean = false) {
  return {
    type: GET_CONFIG_SETTING.base,
    payload: getConfig(url, refresh),
  };
}

async function getConfig(url: string, refresh: boolean) {
  const provider = new RemoteConfigProvider(url);
  return provider.getConfig(refresh);
}
