import {
  getHourlyForecastByLatLon,
  getDailyForecastByLatLon,
} from '../../common/actions/forecastAction';
import { getCurrentStationObsByLatLon } from '../../common/actions/currentStationObsAction';
import { getLocationDetailsByLatLon } from '../../common/actions/locationDetailsAction';
import { getAlertsByLatLon } from '../../common/actions/alertsActions';

export function loadDataForLatLong(latitude: number, longitude: number) {
  return dispatch => {
    return Promise.all([
      dispatch(
        getHourlyForecastByLatLon({
          latitude,
          longitude,
        }),
      ),
      dispatch(getDailyForecastByLatLon({ latitude, longitude })),
      dispatch(
        getCurrentStationObsByLatLon({
          latitude,
          longitude,
        }),
      ),
      dispatch(getLocationDetailsByLatLon({ latitude, longitude })),
      dispatch(getAlertsByLatLon({ latitude, longitude })),
    ]);
  };
}
