/* @flow */

import React, { Component } from 'react';
import styled from 'styled-components';

import { utilities } from '@weatherbug/common';

import { WeatherIcon } from '../shared/WeatherIcon';
import { Text1, Text2, Text3 } from '../shared/Typography';
import { Box } from '../shared/Box';
import { Table, TableRow } from './Table';
import { HourlyItemBox } from './HourlyItemBox';

import { TemperatureLabel } from '../shared/TemperatureLabel';
import type { HourlyForecastEntry } from '../../common/actions/forecastAction';

type Pick<Origin: Object, Keys: Object> = $ObjMapi<
  Keys,
  <Key>(k: Key) => $ElementType<Origin, Key>,
>;

export type HourEntry = Pick<
  HourlyForecastEntry,
  {
    iconCode: any,
    temperature: any,
    forecastDateLocalStr: any,
    precipProbability: any,
    description: any,
    relativeHumidity: any,
    windDirectionDegrees: any,
    windSpeed: any,
  },
>;

type Props = {
  hourEntry: HourEntry,
};
export class HourlyWeatherColumn extends Component<Props> {
  render() {
    const { hourEntry } = this.props;
    const {
      iconCode,
      temperature,
      description,
      precipProbability,
      relativeHumidity,
      windDirectionDegrees,
      windSpeed,
    } = hourEntry;
    const date = new Date(hourEntry.forecastDateLocalStr);
    const rawHourNum = date.getHours();
    const hourNum = rawHourNum % 12 === 0 ? 12 : rawHourNum % 12;
    const title = hourNum + ' ' + (rawHourNum < 12 ? 'AM' : 'PM');
    const wind = `${utilities.getWindDirection(windDirectionDegrees)} ${windSpeed}`;

    return (
      <HourlyItemBox>
        <Box marginBottom={1}>
          <Text2>{title}</Text2>
        </Box>

        <Box marginBottom={1}>
          <Text1>
            <TemperatureLabel value={temperature} />
          </Text1>
        </Box>

        <Box height={32} display="flex" alignItems="center">
          <Text3 lineClamp={2}>{description || '--'}</Text3>
        </Box>

        <Box display="flex" flex={1} alignItems="center" justifyContent="center">
          <IconContainer>
            {iconCode && (
              <WeatherIcon size="small" iconCode={hourEntry.iconCode}>
                {({ src }) => <IconImg src={src} />}
              </WeatherIcon>
            )}
          </IconContainer>
        </Box>

        <Table>
          {typeof precipProbability === 'number' && (
            <TableRow label="Precip." value={precipProbability + '%'} />
          )}
          {typeof relativeHumidity === 'number' && (
            <TableRow label="Humidity" value={relativeHumidity + '%'} />
          )}
          {!!wind && <TableRow label="Wind" value={wind} />}
        </Table>
      </HourlyItemBox>
    );
  }
}

const IconImg = styled.img`
  width: 100%;
`;

const IconContainer = styled.div`
  width: 64px;
  height: 64px;
  border-radius: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  // background: rgba(255, 255, 255, 0.2);
`;
