import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import { WeatherIcon } from '../shared/WeatherIcon';
import { MONTHS } from '../../common/utilities/date';
import { Title1, Title2, Text2 } from '../shared/Typography';
import { getHourlyForecastByLatLon } from '../../common/actions/forecastAction';
import type { State as UserConfigState } from '../../common/reducers/userConfigReducer';
import { Box } from '../shared/Box';
import { TemperatureLabel } from '../shared/TemperatureLabel';
import { LoadingSpinner } from '../shared/LoadingSpinner';

type Props = {
  match: Match,
  history: History,
  locationName: ?string,
  // dispatch: Dispatch,
  loading: boolean,
  getHourlyForecastByLatLon: typeof getHourlyForecastByLatLon,
  nowForecast: ?{
    temperature: number,
    high: ?number,
    low: ?number,
    summary: ?string,
    iconCode: ?number,
  },
  temperatureUnit: $PropertyType<UserConfigState, 'temperatureUnit'>,
  observationDate: ?Date,
};

function getFormattedTime(date: Date) {
  return `${formatHour(date.getHours())}:${formatMinutes(date.getMinutes())} ${getAmOrPm(
    date.getHours(),
  )}`;
}

function formatMinutes(rawMinutesNum: number): string {
  return (rawMinutesNum <= 9 ? '0' : '') + rawMinutesNum;
}

function formatHour(rawHourNum: number) {
  return rawHourNum % 12 === 0 ? 12 : rawHourNum % 12;
}

function getAmOrPm(rawHourNum: number) {
  return rawHourNum < 12 ? 'AM' : 'PM';
}

const NowViewBare = ({ nowForecast, observationDate, locationName, className }: Props) => {
  const currentDate =
    observationDate &&
    `${
      MONTHS[observationDate.getMonth()]
    } ${observationDate.getDate()}, ${observationDate.getFullYear()}`;
  const currentTime = observationDate && getFormattedTime(observationDate);

  if (!nowForecast) {
    return (
      <Container className={className}>
        <Box display="flex" alignItems="center" justifyContent="center" height="100%">
          <LoadingSpinner />
        </Box>
      </Container>
    );
  }

  return (
    <Container className={className}>
      <Box marginBottom={2}>
        <Title2>{locationName}</Title2>
      </Box>

      <Box marginBottom={3} display="flex" flexDirection="row">
        <Box marginRight={3}>
          <Text2>{currentDate}</Text2>
        </Box>
        <Text2>{currentTime}</Text2>
      </Box>

      <Box display="flex" flexDirection="row">
        <Box flex={1}>
          <DisplayContainer>
            <TemperatureLabel value={nowForecast.temperature}>
              {({ temperature, degree, unit }) => (
                <Title1>
                  {temperature}
                  <span
                    style={{ fontSize: 28, verticalAlign: 'top', position: 'relative', top: 8 }}
                  >
                    {degree}
                    {unit}
                  </span>
                </Title1>
              )}
            </TemperatureLabel>
          </DisplayContainer>

          <Box>
            {typeof nowForecast.feelsLike === 'number' && (
              <Box marginBottom={0.5}>
                <Text2>
                  Feels like <TemperatureLabel value={nowForecast.feelsLike} />
                </Text2>
              </Box>
            )}

            <Text2>
              Hi{' '}
              <TemperatureLabel
                value={typeof nowForecast.high === 'number' ? nowForecast.high : '--'}
              />{' '}
              | Lo{' '}
              <TemperatureLabel
                value={typeof nowForecast.low === 'number' ? nowForecast.low : '--'}
              />
            </Text2>
          </Box>
        </Box>

        <Box display="flex" flexDirection="column" flex={1} alignItems="center">
          <DisplayContainer>
            <IconContainer>
              <WeatherIcon size="small" iconCode={nowForecast.iconCode}>
                {({ src }) => <IconImg alt={nowForecast.iconCode} src={src} />}
              </WeatherIcon>
            </IconContainer>
          </DisplayContainer>
          <Text2>{nowForecast.iconDescription}</Text2>
        </Box>
      </Box>
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  max-width: 376px;
  margin: 0 auto;
`;

const IconImg = styled.img`
  width: 100%;
`;

const DisplayContainer = styled.div`
  width: 104px;
  height: 104px;
`;

const IconContainer = styled.div`
  border-radius: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const select = store => {
  const { forecast, currentStationObs, locationDetails } = store;
  const hourlyForecast = (forecast && forecast.hourly) || undefined;

  const { observation: nowObservation, highLow: nowHighLow } =
    (currentStationObs && currentStationObs.currentStationObs) || {};
  const nowHourlyForecast =
    (hourlyForecast &&
      hourlyForecast.hourlyForecastPeriod &&
      hourlyForecast.hourlyForecastPeriod[0]) ||
    undefined;
  const nowForecast =
    nowObservation && nowHourlyForecast
      ? {
          temperature: Math.round(nowObservation.temperature),
          high: nowHighLow ? Math.round(nowHighLow.temperatureHigh) : undefined,
          low: nowHighLow ? Math.round(nowHighLow.temperatureLow) : undefined,
          feelsLike: nowObservation.feelsLike,
          summary: nowHourlyForecast.summaryDescription || undefined,
          iconCode: nowObservation.iconCode || undefined,
          iconDescription: nowObservation.iconDescription || undefined,
        }
      : undefined;

  return {
    nowForecast,
    locationName:
      locationDetails &&
      locationDetails.locationDetails &&
      locationDetails.locationDetails.displayCompositeName,
    observationDate:
      (nowObservation && new Date(nowObservation.observationTimeAdjustedLocalStr)) || undefined,
  };
};

function mapActions(dispatch) {
  return bindActionCreators(
    {
      getHourlyForecastByLatLon,
    },
    dispatch,
  );
}

export const NowView = styled(
  connect(
    select,
    mapActions,
  )(NowViewBare),
)``;
