/* @flow */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { RasterLayer } from '@weatherbug/react';
import { Map } from './map';

import styled from 'styled-components';
import {
  getStandardApiConfigFromStore,
  getMapApiConfigFromStore,
} from '../../common/config/getStandardApiConfig';

import { LoadingSpinner } from '../shared/LoadingSpinner';

type Props = {
  authId: string,
  secret: string,
  stdParameters: Object,
  mapboxAccessToken: string,

  locationName: ?string,
  locationSlugName: ?string,

  sidebarContent: ?React$Element<any>,
  pageTitle: string,

  latitude?: number,
  longitude?: number,
  defaultLayerId: string,
  defaultZoom: number,
  className?: string,
};

// const layers = [
//   'lxflash-radar-consumer-web',
//   'Radar.Global', // "Radar"
//   'Contour.Observed.Pollen.Blur', // "Pollen"
//   'GlobalSatellite',
//   'en-alerts',
//   'nws-alerts',
//   'lxflash-consumer', // "Lightning"
//   'Contour.Observed.DailyRain',
//   'Contour.Observed.Temperature',
//   'Observed.Temperature',
//   'Contour.Observed.Pressure.SeaLevel',
//   'Observed.Pressure.SeaLevel',
//   'Contour.Observed.Temperature.HeatIndex',
//   'Contour.Observed.WindChill',
//   'Contour.Observed.Humidity',
//   'Contour.Observed.WindSpeed',
//   'Contour.Observed.DewPoint',
// ]

class _MapView extends Component<Props> {
  onClick = () => {
    const newTab = window.open('about:blank', '_blank');
    const { locationSlugName } = this.props;
    if (newTab && newTab.location && locationSlugName) {
      newTab.location.href = `https://www.weatherbug.com/maps/${locationSlugName}?layerId=Radar.Global`;
    }
  };

  render() {
    const {
      authId,
      secret,
      mapboxAccessToken,
      stdParameters,
      defaultLayerId,
      defaultZoom,
      longitude,
      latitude,
      className,
    } = this.props;

    if (typeof latitude !== 'number' || typeof longitude !== 'number') {
      return (
        <LoadingContainer className={className}>
          <LoadingSpinner />
        </LoadingContainer>
      );
    }

    return (
      <Container className={className} onClick={this.onClick}>
        <Map
          latitude={latitude}
          longitude={longitude}
          initialLatitude={latitude}
          initialLongitude={longitude}
          mapboxAccessToken={mapboxAccessToken}
          showCenterMarker={true}
          enablePan={false}
          enableZoom={false}
          initialZoom={defaultZoom}
          smallPinImageUrl="img/map-pin-active.png"
          bigPinImageUrl="img/map-pin-active.png"
          extension={(
            map,
            latitude,
            longitude,
            mapZoomOutPressed,
            mapZoomInPressed,
            mapPanToMarkerPressed,
            currentZoomLevel,
            changeZoom,
          ) => {
            const tempUnits = 1; //'C';
            const pressureUnits = 1; //'inHg';
            const rainUnits = 2; //'mm';
            const windUnits = 1; //'mph';
            return (
              <RasterLayer
                map={map}
                latitude={latitude}
                longitude={longitude}
                mapBaseUrl={this.props.mapApiUrl}
                tileBaseUrl={this.props.tileBaseUrl}
                authId={authId}
                secret={secret}
                layerOpacity={70}
                stdApiParameters={stdParameters}
                defaultLayerId={defaultLayerId}
                tempUnit={tempUnits}
                pressureUnit={pressureUnits}
                rainUnit={rainUnits}
                windUnit={windUnits}
              />
            );
          }}
        />
      </Container>
    );
  }
}

const Container = styled.div`
  width: 100%;
  height: 100%;
  cursor: pointer;
  & > div {
    height: 100%; // This is to work around a bug with the <Map /> component
  }
`;

const LoadingContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;

function select(store) {
  const { remoteConfig, locationDetails } = store;
  const { authId, secret, stdParameters } = getStandardApiConfigFromStore(store);
  const { baseUrl: mapApiUrl, tileBaseUrl } = getMapApiConfigFromStore(store);

  const mapboxAccessToken =
    remoteConfig && remoteConfig.config && remoteConfig.config.MAPBOX_ACCESS_TOKEN;
  if (!mapboxAccessToken) {
    throw new Error('Missing Mapbox Access Token');
  }

  const locationSlugName: ?string =
    locationDetails && locationDetails.locationDetails && locationDetails.locationDetails.slugName;
  return {
    locationSlugName,
    mapboxAccessToken,
    authId,
    secret,
    stdParameters,
    mapApiUrl,
    tileBaseUrl,
  };
}

export const MapView = styled(connect(select)(_MapView))``;
