import React, { useState, useRef } from 'react';
import styled from 'styled-components';
import Measure from 'react-measure';
import { clamp } from 'lodash';

import { Box } from './Box';
import { Icon } from './Icon';

type Props = {
  header: React.ReactNode,
  children: React.ReactNode,
};
export const Carousel = function(props: Props) {
  const { header, children } = props;
  const [childSize, setChildSize] = useState(null);
  const [isMouseDown, setIsMouseDown] = useState(false);
  const [draggableState, setDraggableState] = useState(null);
  
  // translateX drives view logic (enable / disable buttons), it may not be the exact scroll position
  const [translateX, setStateTranslateX] = useState(0);
  const scrollableEl = useRef(null);

  function setTranslateX(position: number, animated = false) {
    if (animated) {
      scrollableEl.current.scrollTo({
        left: position,
        behavior: 'smooth',
      });
      // When scroll is animated, the state translateX is updated via onScroll callback
    } else {
      scrollableEl.current.scrollLeft = position;
      setStateTranslateX(position);
    }
  }

  function getMaxTranslate(containerBounds) {
    if (!childSize) {
      return 0;
    }
    return childSize.width - containerBounds.width;
  }

  function getExactTranslate() {
    if (scrollableEl.current) {
      return scrollableEl.current.scrollLeft;
    } else {
      return translateX;
    }
  }

  return (
    <Measure>
      {({ measureRef: containerRef, contentRect: containerContentRect }) => {
        const viewportSize = containerContentRect.entry.width;
        const maxTranslate = getMaxTranslate(containerContentRect.entry);
        const minTranslate = 0;
        const canGoNextPage = translateX < maxTranslate;
        const canGoPrevPage = translateX > minTranslate;
        const hideNavButtons = !canGoNextPage && !canGoPrevPage;

        function clampTranslate(translate: number) {
          return clamp(translate, minTranslate, maxTranslate);
        }
        function nextPage() {
          const newTranslate = clampTranslate(getExactTranslate() + viewportSize);
          setTranslateX(newTranslate, true);
        }

        function prevPage() {
          const newTranslate = clampTranslate(getExactTranslate() - viewportSize);
          setTranslateX(newTranslate, true);
        }

        function onMouseDown(e) {
          setIsMouseDown(true);
          setDraggableState({
            translateStart: getExactTranslate(),
            dragStart: e.pageX,
          });
        }

        function onMouseUp() {
          setIsMouseDown(false);
          setDraggableState(null);
        }

        // TODO - debounce this by 10-50ms?
        function onMouseMove(e) {
          if (isMouseDown && draggableState !== null) {
            const dragCurrent = e.pageX;
            const deltaX = draggableState.dragStart - dragCurrent;
            const newPosition = draggableState.translateStart + deltaX;
            setTranslateX(clampTranslate(newPosition), false);
          }
        }

        function onScroll(e) {
          if (!isMouseDown) {
            setStateTranslateX(e.target.scrollLeft);
          }
        }

        return (
          <Container ref={containerRef}>
            <Box display="flex" flexDirection="row" marginBottom={1}>
              <Box flex={1}>{header}</Box>
              {!hideNavButtons && (
                <PageButtonContainer>
                  <PageButton onClick={() => prevPage()} isDisabled={!canGoPrevPage}>
                    <Icon name="ChevronLeft" color="white" size={16} />
                  </PageButton>
                  <PageButton onClick={() => nextPage()} isDisabled={!canGoNextPage}>
                    <Icon name="ChevronRight" color="white" size={16} />
                  </PageButton>
                </PageButtonContainer>
              )}
            </Box>

            <Measure
              onResize={({ entry }) => {
                setChildSize(entry);
              }}
            >
              {({ measureRef }) => (
                <ScrollableContainer
                  ref={scrollableEl}
                  onMouseDown={onMouseDown}
                  onMouseUp={onMouseUp}
                  onMouseMove={onMouseMove}
                  onScroll={onScroll}
                >
                  <Box ref={measureRef} display="inline-flex" flexDirection="row">
                    {children}
                  </Box>
                </ScrollableContainer>
              )}
            </Measure>
          </Container>
        );
      }}
    </Measure>
  );
};

const Container = styled.div`
  width: 100%;
  overflow: hidden;
`;

const ScrollableContainer = styled.div`
  overflow-x: scroll;
  overflow-y: hidden;
  ::-webkit-scrollbar {
    width: 0 !important;
  }
  cursor: grab;
  :active {
    cursor: grabbing;
  }
  & > ${Box} * {
    user-select: none;
  }
`;

const PageButton = styled.div`
  opacity: ${props => (props.isDisabled ? 0.4 : 1)};
  user-select: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.1);
  &:hover {
    cursor: pointer;
  }
`;

const PageButtonContainer = styled.div`
  display: flex;
  flex-direction: row;
  & > ${PageButton}:not(:last-child) {
    margin-right: 4px;
  }
`;
