// @flow

import React from 'react';

import { Text2 } from '../shared/Typography';
import { Box } from '../shared/Box';
import { HourlyItemBox } from './HourlyItemBox';
import { MONTHS, DAYS } from '../../common/utilities/date';

type Props = {
  date: Date,
};

export const DayColumn = ({ date }: Props) => {
  const dateLabel = MONTHS[date.getMonth()] + ' ' + date.getDate();
  const dayOfWeek = DAYS[date.getDay()];
  return (
    <HourlyItemBox>
      <Box
        flex={1}
        display="flex"
        flexDirection="column"
        justifyContent="center"
        alignItems="center"
      >
        <Box marginBottom={2}>
          <Text2 semiBold>{dateLabel}</Text2>
        </Box>
        <Text2 semiBold>{dayOfWeek}</Text2>
      </Box>
    </HourlyItemBox>
  );
};
