// @flow

import { dataLayer } from '@weatherbug/common';

import {
  getStandardApiConfig,
  type StandardApiConfigT,
} from '../../common/config/getStandardApiConfig';

const DEFAULT_VERBOSE = true;

type Datestring = string;

export type GetAllIconUrlsPayload = {
  Height: 168,
  IconByCodeUrl: 'https://d2erwcr27wae6d.cloudfront.net/resources/v1/resource/IconByCodeV1?iconset=forecast&iconSize=4&iconCode={iconCode}&token=1c08bc8a-76dc-425b-a367-46c75cec42de',
  IconByCodeUrlValidForSeconds: 518400,
  Icons: Array<{
    IconCode: number,
    Url: string,
  }>,
  LastUpdatedDateTimeUtc: Datestring,
  Width: number,
  ZipUrl: string,
  responseTime: number,
};
export function getAllIconUrls({
  iconSet = 'forecast',
  iconSize = 4,
  contrast = null,
  verbose = DEFAULT_VERBOSE,
  refresh = false,
}: {
  iconSet?: 'forecast' | string,
  iconSize?: 4 | number,
  contrast?: null,
  verbose?: boolean,
  refresh?: boolean,
}) {
  return function getAllIconUrlsThunk(dispatch) {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { ICON_API_URL, authId, secret, stdParameters } = apiConfig;
    return dispatch(
      dataLayer.actions.getAllIconUrls(
        iconSet,
        iconSize,
        contrast,
        ICON_API_URL,
        authId,
        secret,
        stdParameters,
        verbose,
        refresh,
      ),
    );
  };
}
