import React, { memo } from 'react';
import styled from 'styled-components';
import { bindActionCreators } from 'redux';
import { logEvent } from '../../common/actions/logActions';
import { connect } from 'react-redux';
import { CCPABanner } from './ccpaBanner';
import { FeedbackBanner } from './FeedbackBanner';

const TERMS_URL = 'https://www.weatherbug.com/legal/terms';
const PRIVACY_POLICY_URL = 'https://www.weatherbug.com/legal/privacy';

const _LegalBanner = memo(({ className, logEvent }) => {
  const currentYear = new Date().getFullYear();

  function onTermsOrPolicyClick(url) {
    logEvent('user.link.clicked', {
      url,
    });
  }

  return (
    <Container className={className}>
      <span>
        &copy; GroundTruth, {currentYear}. WeatherBug is a brand of GroundTruth. All Rights
        Reserved. &nbsp;
      </span>
      <a href={TERMS_URL} onClick={() => onTermsOrPolicyClick(TERMS_URL)}>
        Terms
      </a>{' '}
      |{' '}
      <a href={PRIVACY_POLICY_URL} onClick={() => onTermsOrPolicyClick(PRIVACY_POLICY_URL)}>
        Policy
      </a>{' '}
      <CCPABanner />
      <FeedbackBanner />
    </Container>
  );
});

function mapActions(dispatch) {
  return bindActionCreators(
    {
      logEvent,
    },
    dispatch,
  );
}

export const LegalBanner = connect(null, mapActions)(_LegalBanner);

const Container = styled.div`
  background-color: white;
  padding: 8px;
  text-align: center;
  color: #0083c6;
  font-size: 14px;
  a {
    color: #0083c6;
  }
`;
