// @flow

import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import OutsideClickHandler from 'react-outside-click-handler';

import { getAlertsLegend } from '../../common/actions/alertsActions';
import { Icon } from '../shared/Icon';
import { Popover } from '../shared/Popover';
import { Box } from '../shared/Box';
import { AlertCard, Title, Description } from '../alerts/AlertCard';
import { logEvent } from '../../common/actions/logActions';

import type { AlertT } from '../alerts/AlertCard';

type Props = {
  alerts: Array<AlertT>,
  latitude: number,
  longitude: number,
  locationSlugName: string,
  logEvent: typeof logEvent,
  getAlertsLegend: typeof getAlertsLegend,
};

const _AlertsWidget = function(props: Props) {
  const { alerts, locationSlugName, latitude, longitude, getAlertsLegend } = props;
  const [isOpen, setIsOpen] = useState(false);
  const [alertColorMap, setAlertColorMap] = useState({});

  useEffect(() => {
    const fetchData = async () => {
      const legendResults = await getAlertsLegend(latitude, longitude);
      const legendMap = legendResults.reduce((obj, item) => {
        obj[item.Type] = item;
        return obj;
      }, {});
      setAlertColorMap(legendMap);
    };
    if (latitude && longitude && alerts.length > 0) {
      fetchData();
    }
  }, [latitude, longitude, alerts.length, getAlertsLegend]);

  function toggleAlertPopover() {
    setIsOpen(!isOpen);
  }

  function onClickAlert() {
    const newTab = window.open('about:blank', '_blank');
    if (newTab && newTab.location && locationSlugName) {
      const url = `https://www.weatherbug.com/alerts/${locationSlugName}`;
      props.logEvent('user.link.clicked', {
        url,
      });
      newTab.location.href = url;
    }
  }

  return (
    <Container>
      <OutsideClickHandler
        onOutsideClick={() => {
          setIsOpen(false);
        }}
      >
        <InnerContainer>
          <Popover
            isOpen={isOpen}
            offset={{ top: 4, right: 4 }}
            anchorElement={
              <IconContainer onClick={toggleAlertPopover}>
                <Icon
                  name="WarningEmpty"
                  size={28}
                  color={alerts.length === 0 ? '#2f6f8c' : '#d25858'}
                />
                <IconLabel>{alerts.length}</IconLabel>
              </IconContainer>
            }
          >
            <Box width={365}>
              {alerts.length === 0 && (
                <Box padding={2} display="flex" direction="row" alignItems="center">
                  <Box width={50} height={50} marginRight={2}>
                    <img src={require('../../assets/sun.svg')} alt="sun" />
                  </Box>
                  <Box onClick={onClickAlert}>
                    <Title>Enjoy the Weather</Title>
                    <Description>There are no alerts in your area.</Description>
                  </Box>
                </Box>
              )}
              {alerts.map(alert => (
                <AlertCard
                  key={alert.AlertId}
                  theme={alertColorMap[alert.AlertTypeName]}
                  alert={alert}
                  onClick={onClickAlert}
                />
              ))}
            </Box>
          </Popover>
        </InnerContainer>
      </OutsideClickHandler>
    </Container>
  );
};

const Container = styled.div`
  align-self: stretch;
  &:hover {
    cursor: pointer;
    background-color: rgb(245, 245, 245);
  }
  & > div {
    // this is the outside click handler
    height: 100%;
  }
`;
const InnerContainer = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  height: 100%;
`;

const IconLabel = styled.div`
  color: white;
  font-size: 12px;
  font-weight: bold;
`;

const IconContainer = styled.div`
  padding: 0 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: stretch;

  ${IconLabel} {
    position: absolute;
    margin-top: 1px;
  }
`;

const select = function(store) {
  const { alerts, locationDetails } = store;
  return {
    alerts: (alerts && alerts.alerts && alerts.alerts.alertList) || [],
    latitude:
      locationDetails &&
      locationDetails.locationDetails &&
      locationDetails.locationDetails.latitude,
    longitude:
      locationDetails &&
      locationDetails.locationDetails &&
      locationDetails.locationDetails.longitude,
    locationSlugName:
      locationDetails &&
      locationDetails.locationDetails &&
      locationDetails.locationDetails.slugName,
  };
};

function mapActions(dispatch) {
  return bindActionCreators(
    {
      getAlertsLegend,
      logEvent,
    },
    dispatch,
  );
}

export const AlertsWidget = connect(
  select,
  mapActions,
)(_AlertsWidget);
