/* @flow */

import { getAppVersion, getAppId } from '../../modules/main/AppRegistration';

import type { ReduxState } from '../reducers/index.js';

export type StdParametersT = {
  application: null | string,
  appVersion: 'unknown' | string,
};

const stdParameters = {
  application: getAppId(),
  appVersion: getAppVersion(),
};

export type StandardApiConfigT = {
  baseUrl: string,
  authId: string,
  secret: string,
  stdParameters: StdParametersT,

  LOCATIONS_API_URL: string,
  TRAFFIC_API_URL: string,
  POLLEN_API_URL: string,
  AQI_API_URL: string,
  OBSERVATIONS_API_URL: string,
  FORECAST_API_URL: string,
  ALERTS_API_URL: string,
  LEGAL_API_URL: string,
  LOGGING_API_URL: string,
  ADTARGETING_API_URL: string,

  ICON_API_URL: string,
  BACKGROUND_IMAGE_API_URL: string,
  ENTITY_MANAGEMENT_API_URL: string,
};
export function getStandardApiConfigFromStore(store: ReduxState): StandardApiConfigT {
  const {
    API_BASE_URL: baseUrl,

    LOCATIONS_API_URL,
    TRAFFIC_API_URL,
    POLLEN_API_URL,
    AQI_API_URL,
    OBSERVATIONS_API_URL,
    FORECAST_API_URL,
    ALERTS_API_URL,
    LEGAL_API_URL,
    LOGGING_API_URL,
    ADTARGETING_API_URL,
    ICON_API_URL,
    BACKGROUND_IMAGE_API_URL,
    ENTITY_MANAGEMENT_API_URL,
    FEEDBACK_URL,
    AUTH_ID: authId,
    AUTH_SECRET: secret,
  } = store.remoteConfig.config;

  return {
    baseUrl,
    authId,
    secret,
    stdParameters,

    LOCATIONS_API_URL,
    TRAFFIC_API_URL,
    POLLEN_API_URL,
    AQI_API_URL,
    OBSERVATIONS_API_URL,
    FORECAST_API_URL,
    ALERTS_API_URL,
    LEGAL_API_URL,
    LOGGING_API_URL,
    ADTARGETING_API_URL,

    ICON_API_URL,
    BACKGROUND_IMAGE_API_URL,
    ENTITY_MANAGEMENT_API_URL,
    FEEDBACK_URL,
  };
}

export function getStandardApiConfig() {
  return function getStandardApiConfigThunk(dispatch, getStore: () => ReduxState) {
    return getStandardApiConfigFromStore(getStore());
  };
}

export function getMapApiConfigFromStore(store: ReduxState) {
  const {
    WB_MAPS_BASE_URL: mapBaseUrl,
    AUTH_ID: authId,
    AUTH_SECRET: secret,
    WB_TILE_BASE_URL: tileBaseUrl,
  } = store.remoteConfig.config;
  return {
    baseUrl: mapBaseUrl,
    tileBaseUrl,
    authId,
    secret,
    stdParameters,
  };
}

export function getMapApiConfig() {
  return (dispatch, getStore: () => ReduxState) => {
    return getMapApiConfigFromStore(getStore());
  };
}
