// @flow

import styled, { css } from 'styled-components';
import { space } from './Spacing';

type Props = {
  margin?: number | string,
  marginTop?: number,
  marginRight?: number,
  marginBottom?: number,
  marginLeft?: number,

  padding?: number,
  paddingTop?: number,
  paddingRight?: number,
  paddingBottom?: number,
  paddingLeft?: number,

  position?: string,
  flex?: number,
  flexDirection?: string,
  display?: string,
  alignItems?: string,
  justifyContent?: string,
  justifySelf?: string,
  alignSelf?: string,
  fontSize?: number | string,
  flexGrow?: number,

  width?: number | string,
  height?: number | string,
};

const getProperty = function (propName: string, cssName: string) {
  return (props: Props) => {
    return propName in props && `${cssName}: ${props[propName]};`;
  };
};
const getSpaceProperty = function (propName: string, cssName: string) {
  return (props: Props) => {
    return typeof props[propName] === 'number' && `${cssName}: ${space(props[propName])};`;
  };
};
const getSizeProperty = function (name: string) {
  return (props: Props) => {
    if (typeof props[name] === 'number') {
      return `${name}: ${props[name]}px;`;
    } else {
      return `${name}: ${props[name]}`;
    }
  };
};

export const Box = styled.div`
  ${getProperty('position', 'position')}
  ${getProperty('display', 'display')}
  ${getProperty('alignItems', 'align-items')}
  ${getProperty('flexDirection', 'flex-direction')}
  ${getProperty('justifyContent', 'justify-content')}
  ${getProperty('justifySelf', 'justify-self')}
  ${getProperty('alignSelf', 'align-self')}
  ${getProperty('flexGrow', 'flex-grow')}
  ${getSizeProperty('width')}
  ${getSizeProperty('height')}
  ${getProperty('fontSize', 'font-size')}

  ${({ flex }) =>
    flex &&
    css`
      flex: ${flex} 1 auto;
    `}

  ${getProperty('margin', 'margin')}
  ${getSpaceProperty('marginTop', 'margin-top')}
  ${getSpaceProperty('marginRight', 'margin-right')}
  ${getSpaceProperty('marginBottom', 'margin-bottom')}
  ${getSpaceProperty('marginLeft', 'margin-left')}

  ${getSpaceProperty('padding', 'padding')}
  ${getSpaceProperty('paddingTop', 'padding-top')}
  ${getSpaceProperty('paddingRight', 'padding-right')}
  ${getSpaceProperty('paddingBottom', 'padding-bottom')}
  ${getSpaceProperty('paddingLeft', 'padding-left')}
`;
