import React, { memo, useState, useEffect } from 'react';
import styled from 'styled-components';
import { bindActionCreators } from 'redux';
import { setCCPAOptOutStatus } from '../../common/actions/ccpaActions';
import { updateCustomSettings } from '../../common/actions/entityManagementActions';
import { connect } from 'react-redux';
import LocalStorage from '../shared/LocalStorage';
import { useSelector, useDispatch } from 'react-redux';
import { getComplianceInfo } from '../../common/actions/complianceActions';

const _CCPABanner = memo(
  ({ className, logEvent, setCCPAOptOutStatus, updateCustomSettings, latitude, longitude }) => {
    const TEXT_DONT_SELL = 'Do not sell';
    const TEXT_NOT_SELLING = 'Not Selling your Data';
    const TEXT_CONFIRMED = 'Confirmed';
    const ifOptedOut = LocalStorage.getCCPAOptOutStatus();
    const [ccpaText, setCcpaText] = useState(ifOptedOut ? TEXT_NOT_SELLING : TEXT_DONT_SELL);
    const isCCPAEnabled = useSelector(store => {
      if (store && store.complianceInfo.complianceInfo) {
        return store.complianceInfo.complianceInfo.ccpa;
      }
      return false;
    });
    const dispatch = useDispatch();
    useEffect(() => {
      if (latitude && longitude) {
        dispatch(getComplianceInfo(latitude, longitude));
      }
    }, [latitude, longitude, dispatch]);

    const onCCPAClicked = () => {
      if (LocalStorage.getCCPAOptOutStatus()) {
        return;
      }
      updateSettings();
      setCCPAOptOutStatus(true);
      LocalStorage.setCCPAOptOutStatus(true);
      setCcpaText(TEXT_CONFIRMED);
      setTimeout(() => {
        changeCCPAText();
      }, 10000);
    };

    async function updateSettings() {
      const appInstanceId = await LocalStorage.getAppInstanceId();
      updateCustomSettings({
        appInstanceId,
        key: 'CCPA',
        value: { value: isCCPAEnabled },
        verbose: true,
      });
    }

    const changeCCPAText = () => {
      if (LocalStorage.getCCPAOptOutStatus()) {
        setCcpaText(TEXT_NOT_SELLING);
      } else {
        setCcpaText(TEXT_DONT_SELL);
      }
    };

    return (
      <Container className={Container}>
        {isCCPAEnabled && (
          <>
            | &nbsp;
            <Label onClick={onCCPAClicked}>{ccpaText}</Label>
          </>
        )}
      </Container>
    );
  },
);

const select = store => {
  const { complianceInfo } = store.complianceInfo || {};
  const { latitude, longitude } = store.locationDetails.userDefaultLocation || {};
  return { complianceInfo, latitude, longitude };
};

function mapActions(dispatch) {
  return bindActionCreators(
    {
      setCCPAOptOutStatus,
      updateCustomSettings,
    },
    dispatch,
  );
}

export const CCPABanner = connect(
  select,
  mapActions,
)(_CCPABanner);

CCPABanner.displayName = 'CCPABanner';

const Container = styled.div`
  font-size: 14px;
  display: inline;
  font-color: #0083c6;
  cursor: pointer;
`;

const Label = styled.span`
  text-decoration-line: underline;
}`;
