// @flow

import React from 'react';
import styled from 'styled-components';

import ChevronLeft from '../../generated-svg-components/ChevronLeft.js';
import ChevronRight from '../../generated-svg-components/ChevronRight.js';
import Search from '../../generated-svg-components/Search.js';
import WarningEmpty from '../../generated-svg-components/WarningEmpty.js';
import ArrowRight from '../../generated-svg-components/ArrowRight.js';

const IconComponents = {
  ChevronLeft,
  ChevronRight,
  Search,
  WarningEmpty,
  ArrowRight,
};

type Props = {
  name: $Keys<typeof IconComponents>,
  size?: number,
  color?: string,
  className?: any,
};
export const Icon = styled(({ size, name, className }: Props) => {
  const Component = IconComponents[name];
  return <Component className={className} viewBox="0 0 24 24" width={size} height={size} />;
})`
  & > path {
    fill: ${(props: Props) => props.color || '#888888'};
  }
`;
