import React from 'react';
import _ from 'lodash';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import { DailyWeatherColumn, EmptyDailyColumn } from './DailyWeatherColumn';

import { Title3 } from '../shared/Typography';
import { Carousel } from '../shared/Carousel';

type Props = {
  match: Match,
  history: History,
  location: Location,
  // dispatch: Dispatch,
  loading: boolean,
  dailyEntries: Array<{
    high: ?number,
    low: ?number,
    summary: string,
    iconCode: number,
    forecastDateLocalStr: string,
  }>,
};

const DailyViewBare = ({ dailyEntries }: Props) => {
  return (
    <Carousel header={<Title3>10 DAY</Title3>}>
      {dailyEntries.length === 0 &&
        _.times(10, i => {
          return <EmptyDailyColumn key={i} />;
        })}
      {dailyEntries.map(entry => {
        const key = (entry.dayForecast || entry.nightForecast).forecastDateLocalStr;
        return (
          <DailyWeatherColumn
            key={key}
            dayForecast={entry.dayForecast}
            nightForecast={entry.nightForecast}
          />
        );
      })}
    </Carousel>
  );
};

const select = store => {
  const { forecast } = store;

  const dailyForecast = (forecast && forecast.daily) || undefined;
  const today = new Date();
  today.setUTCHours(0, 0, 0, 0);

  const forecastsByDay = _.groupBy(
    (dailyForecast && dailyForecast.dailyForecastPeriods) || [],
    forecastEntry => {
      const hour = new Date(forecastEntry.forecastDateLocalStr);
      hour.setUTCHours(0, 0, 0, 0);
      return (hour.getTime() - today.getTime()) / (1000 * 60 * 60 * 24);
    },
  );

  const dailyEntries = _.mapValues(forecastsByDay, forecastPair => {
    let dayForecast = undefined;
    let nightForecast = undefined;

    if (forecastPair.length >= 2) {
      dayForecast = forecastPair[0];
      nightForecast = forecastPair[1];
    } else {
      const singleForecast = forecastPair[0];
      if (singleForecast) {
        const singleDate = new Date(singleForecast.forecastDateLocalStr);
        if (singleDate.getHours() <= 12) {
          dayForecast = singleForecast;
        } else {
          nightForecast = singleForecast;
        }
      }
    }
    return {
      dayForecast,
      nightForecast,
    };
  });

  const dailyEntriesArray = _.keys(dailyEntries).reduce((arr, dayOffset) => {
    arr[parseInt(dayOffset, 10)] = dailyEntries[dayOffset];
    return arr;
  }, []);

  return {
    dailyEntries: dailyEntriesArray,
  };
};

function mapActions(dispatch) {
  return bindActionCreators({}, dispatch);
}

export const DailyView = connect(
  select,
  mapActions,
)(DailyViewBare);
