/* @flow */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import styled from 'styled-components';

import { LoadingSpinner } from '../shared/LoadingSpinner';
import { MapView } from './MapView';
import { NowView } from './NowView';
import { AppRegistration } from './AppRegistration';
import { DailyView } from '../daily/DailyView';
import { HeaderBar } from '../header-bar/HeaderBar';
import { LegalBanner } from './LegalBanner';
import { TaboolaView } from './TaboolaView';
import {
  getHourlyForecastByLatLon,
  getDailyForecastByLatLon,
} from '../../common/actions/forecastAction';
import { getUsersLocation } from '../../common/actions/locationSearchActions';
import { getCurrentStationObsByLatLon } from '../../common/actions/currentStationObsAction';
import { getLocationDetailsByLatLon } from '../../common/actions/locationDetailsAction';
import { getAllIconUrls } from '../../common/actions/iconActions';
import { loadDataForLatLong } from '../../common/actions/appActions';
import { getAppInstance } from '../../common/actions/entityManagementActions';
import { logEvent } from '../../common/actions/logActions';

import { HourlyView } from '../hourly/HourlyView';
import { WBBackgroundImage } from '../shared/WBBackgroundImage';
import { Box } from '../shared/Box';
import { WindowSize } from '../shared/WindowSize';
import LocalStorage from '../shared/LocalStorage';

import type { Match, History, Location } from 'react-router';
import { TaboolaAd } from './TaboolaAd';

// if you change REFRESH_AFTER_MIN to be <15 minutes (the current cache time TTL), then you'll need to add logic to force-refresh the various API calls
// made it current cache + 1 minute in case of slow responses, which would make the refresh get called after only 14 minutes
const REFRESH_AFTER_MIN = 16;

type Props = {
  match: Match,
  history: History,
  location: Location,
  loading: boolean,
  getHourlyForecastByLatLon: typeof getHourlyForecastByLatLon,
  getDailyForecastByLatLon: typeof getDailyForecastByLatLon,
  getCurrentStationObsByLatLon: typeof getCurrentStationObsByLatLon,
  getLocationDetailsByLatLon: typeof getLocationDetailsByLatLon,
  loadDataForLatLong: typeof loadDataForLatLong,
  getAllIconUrls: typeof getAllIconUrls,
  getUsersLocation: typeof getUsersLocation,
  logEvent: typeof logEvent,
  latitude: ?number,
  longitude: ?number,
};
type State = {
  loading: boolean,
  isLocationLoaded: boolean,
};

class _MainView extends Component<Props, State> {
  state = {
    loading: true,
    isLocationLoaded: false,
    isNight: false,
  };

  refreshTimeout: ?TimeoutID;

  componentDidMount() {
    this.setupRefreshTimer();

    this.props.logEvent('app.page.load', {
      busDomain: 'Forecast Summary',
      pageName: 'ForecastSummary',
    });

    this.checkForLifecycleEvents();

    this.props.getAllIconUrls({});

    const previousLocation = LocalStorage.getUserSelectedLocation();
    if (previousLocation && previousLocation.Latitude && previousLocation.Longitude) {
      // we can use the previous location
      const { Latitude: latitude, Longitude: longitude } = previousLocation;

      this.setState({
        isLocationLoaded: true,
      });

      this.props.loadDataForLatLong(latitude, longitude);
    } else {
      this.props.getUsersLocation({ refresh: false }).then((response) => {
        if (!response) {
          return null;
        }

        const {
          value: { latitude, longitude },
        } = response;

        this.setState({
          isLocationLoaded: true,
        });

        this.props.loadDataForLatLong(latitude, longitude);
      });
    }
  }

  async checkForLifecycleEvents() {
    const entries: Array<{ localStorageKey: string, eventName: string, data?: Object }> = [
      {
        localStorageKey: 'webext-lifecycle-app.install',
        eventName: 'app.install',
      },
      {
        localStorageKey: 'webext-lifecycle-app.upgrade',
        eventName: 'app.upgrade',
      },
      {
        localStorageKey: 'webext-lifecycle-app.start',
        eventName: 'app.start',
        data: {
          source: '',
          pushMessageId: '',
        },
      },
      {
        localStorageKey: 'webext-lifecycle-app.close',
        eventName: 'app.close',
      },
    ];

    const results = await Promise.all(
      entries.map((entry) => localStorage.getItem(entry.localStorageKey)),
    );
    entries.forEach((entry, index) => {
      const jsonStr = results[index];
      if (jsonStr) {
        const jsonObj = JSON.parse(jsonStr);
        if (jsonObj) {
          if (entry.eventName === 'app.upgrade') {
            this.props.logEvent('app.upgrade', {
              timestamp: jsonObj.timestamp || undefined,
              previousVersion: jsonObj.previousVersion,
              ...(entry.data || {}),
            });
          } else {
            this.props.logEvent(entry.eventName, {
              timestamp: jsonObj.timestamp || undefined,
              ...(entry.data || {}),
            });
          }
          localStorage.removeItem(entry.localStorageKey);
        }
      }
    });
  }

  setupRefreshTimer() {
    if (this.refreshTimeout) {
      return;
    }

    this.refreshTimeout = setTimeout(() => {
      this.refreshTimeout = clearTimeout(this.refreshTimeout);
      const { latitude, longitude } = this.props;
      this.props.loadDataForLatLong(latitude, longitude);
      this.setupRefreshTimer();
    }, REFRESH_AFTER_MIN * 60 * 1000);
  }

  renderLoading() {
    return (
      <div>
        <LoadingSpinner />
      </div>
    );
  }

  onBackgroundImageUrlFetched = (backgroundUrl) => {
    const url = backgroundUrl;
    this.setState({ isNight: url && url.includes('night') });
  };

  renderMain() {
    const { latitude, longitude, complianceInfo } = this.props;
    const shouldDisplayAd = complianceInfo && !complianceInfo.gdpr;
    const { isNight } = this.state;
    const modeIdRightRail = isNight ? 'thumbnails-d-300x250' : 'thumbnails-b-300x250';
    const modeIdBelowThumbnail = isNight ? 'thumbnails-c' : 'thumbnails-a';
    const ccpaStatus = complianceInfo && complianceInfo.ccpa;
    return (
      <Container>
        {this.state.isLocationLoaded && <AppRegistration />}
        <WBBackgroundImage
          latitude={latitude}
          longitude={longitude}
          onBackgroundImageUrlLoaded={this.onBackgroundImageUrlFetched}
        >
          <WindowSize>
            {() => {
              return (
                <LayoutContainer>
                  <HeaderBar />
                  <ScrollContainer>
                    <div style={{ padding: 24 }}>
                      <MainContent>
                        <ResponsiveContent>
                          <NowView />

                          <MapView
                            locationName={'locationName'}
                            defaultLayerId="Radar.Global"
                            defaultZoom={8}
                            latitude={latitude}
                            longitude={longitude}
                            pageTitle="Radar"
                            sidebarContent={undefined}
                          />
                          <TaboolaView
                            modeId={modeIdRightRail}
                            containerId="taboola-right-rail-thumbnails---now-forecast-1x1"
                            placementId="Right Rail Thumbnails - Now Forecast"
                            targetType="mix"
                            divId="taboola-right-rail-thumbnails---now-forecast-1x1"
                            adHeight={250}
                            adWidth={300}
                            isLastSnippet={false}
                            shouldRenderAdView={shouldDisplayAd}
                            ccpa={ccpaStatus}
                          />
                        </ResponsiveContent>

                        <Box marginBottom={3}>
                          <HourlyView />
                        </Box>

                        <DailyView />
                      </MainContent>
                    </div>
                    <TaboolaView
                      modeId={modeIdBelowThumbnail}
                      containerId="taboola-below-content-thumbnails---now-forecast"
                      placementId="Below Content Thumbnails - Now Forecast"
                      targetType={'mix'}
                      divId="taboola-below-content-thumbnails---now-forecast"
                      isLastSnippet={true}
                      shouldRenderAdView={shouldDisplayAd}
                      ccpa={ccpaStatus}
                      shouldSpan={true}
                    />
                  </ScrollContainer>
                  <LegalBanner />
                </LayoutContainer>
              );
            }}
          </WindowSize>
        </WBBackgroundImage>
      </Container>
    );
  }

  render() {
    return <div>{this.renderMain()}</div>;
  }
}

const Container = styled.div`
  background-color: #2f6f8c;
`;

const LayoutContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
`;

const ResponsiveContent = styled.div`
  margin-bottom: 32px;
  display: grid;
  grid-template-columns: 346px 1fr 300px;
  grid-template-areas: 'NowView MapView AdView';
  grid-gap: 24px;
  flex-grow: 1;

  & > ${NowView} {
    grid-area: NowView;
  }

  & > ${MapView} {
    grid-area: MapView;
    height: 250px;
  }

  & > ${TaboolaAd} {
    grid-area: AdView;
    margin: 0 auto;
  }

  @media screen and (max-width: 1024px) {
    grid-template-columns: 1fr 300px;
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
      'NowView NowView'
      'MapView AdView';
  }

  @media screen and (max-width: 635px) {
    grid-template-columns: 1fr;
    grid-template-areas:
      'NowView'
      'MapView'
      'AdView';
  }
`;

const ScrollContainer = styled.div`
  flex-grow: 1;
  overflow-y: scroll;
`;

const MainContent = styled.div`
  max-width: 1200px;
  margin: 0 auto;
`;

const select = (store) => {
  const { complianceInfo } = store.complianceInfo || {};

  if (!store || !store.locationDetails || !store.locationDetails.locationDetails) {
    return {
      loading: true,
    };
  }

  const latitude =
    (store.locationDetails.locationDetails && store.locationDetails.locationDetails.latitude) ||
    undefined;
  const longitude =
    (store.locationDetails.locationDetails && store.locationDetails.locationDetails.longitude) ||
    undefined;

  return { loading: false, latitude, longitude, complianceInfo };
};

function mapActions(dispatch) {
  return bindActionCreators(
    {
      getAllIconUrls,
      getUsersLocation,
      loadDataForLatLong,
      getAppInstance,
      logEvent,
    },
    dispatch,
  );
}

export const MainView = connect(select, mapActions)(_MainView);
