/* @flow */

import { Component } from 'react';
import { connect } from 'react-redux';

type Props = {
  size: 'small' | 'large',
  iconCode: ?number,
  iconUrl: ?string,
  // dispatch: Dispatch,
  children: (params: { src: ?string }) => React$Element<any>,
};
class _WeatherIcon extends Component<Props> {
  render() {
    return this.props.children({
      src: this.props.iconUrl,
    });
  }
}

function select(store, ownProps) {
  const { iconCode } = ownProps;
  const iconEntry = ((store.icon && store.icon.forecastIcons && store.icon.forecastIcons.Icons) ||
    [])[iconCode];
  const iconUrl = iconEntry && iconEntry.Url;
  return {
    iconUrl,
  };
}

export const WeatherIcon = connect(select)(_WeatherIcon);
