// @flow

const scaleNum = 8;

export function _scale(num: number): number {
  return num * scaleNum;
}

export function space(input: number | Array<number>): string {
  if (typeof input === 'number') {
    return `${_scale(input)}px`;
  } else {
    return arrSpace(input);
  }
}

export function arrSpace(arr: Array<number>): string {
  return arr.map(space).join(' ');
}
