// @flow

import { dataLayer } from '@weatherbug/common';

import {
  getStandardApiConfig,
  type StandardApiConfigT,
} from '../../common/config/getStandardApiConfig';

const DEFAULT_VERBOSE = false;

export function getBackgroundImageByLatLon({
  latitude,
  longitude,
  setId,
  verbose = DEFAULT_VERBOSE,
  refresh = false,
}: {
  latitude: number,
  longitude: number,
  setId?: string,
  verbose?: boolean,
  refresh?: boolean,
}) {
  return dispatch => {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { BACKGROUND_IMAGE_API_URL, authId, secret, stdParameters } = apiConfig;
    return dispatch(
      dataLayer.actions.getBackgroundImageByLatLon(
        latitude,
        longitude,
        setId,
        BACKGROUND_IMAGE_API_URL,
        authId,
        secret,
        stdParameters,
        verbose,
        refresh,
      ),
    );
  };
}
