import React, { memo, useState } from 'react';
import styled from 'styled-components';
import { Popover } from '../shared/Popover';
import { Box } from '../shared/Box';
import { FeedbackForm } from './FeedbackForm';

export const FeedbackBanner = memo(() => {
  const [isFeedbackOpen, setIsOpen] = useState(false);

  const setFeedbackFormOpen = () => {
    setIsOpen(!isFeedbackOpen);
  };

  return (
    <Popover
      isOpen={isFeedbackOpen}
      isCentered={true}
      width={440}
      anchorElement={<ClickLink onClick={setFeedbackFormOpen}>Contact Us</ClickLink>}
    >
      <Box width={440}>
        <FeedbackForm setFeedbackFormOpen={setFeedbackFormOpen} />
      </Box>
    </Popover>
  );
});

const ClickLink = styled.span`
  background-color: white;
  padding: 8px;
  text-align: center;
  color: #0083c6;
  font-size: 14px;
  text-decoration: underline;
  margin-left: -2px;
`;
