/* @flow */
import { baseClasses, dataLayer } from '@weatherbug/common';

import {
  getStandardApiConfig,
  type StandardApiConfigT,
} from '../../common/config/getStandardApiConfig';

const { AsyncAction } = baseClasses;
const { EntityManagementProvider } = dataLayer.providers;

const DEFAULT_VERBOSE = true;

export const GET_APP_INSTANCE = new AsyncAction('GET_APP_INSTANCE');
export const UPDATE_LOCATION = new AsyncAction('UPDATE_LOCATION');
export const UPDATE_CUSTOM_USER_SETTINGS = new AsyncAction('UPDATE_CUSTOM_USER_SETTINGS');

export type GetAppInstanceResponseT = {
  appInstanceId: string,
  appInstanceInfo: {
    app: {
      id: string,
      version: string,
    },
    cultureInfo: string,
    installGuid: string,
    os: {
      type: string,
      version: string,
    },
    pushToken: string,
    vendors: Array<{
      id: string,
      name: string,
    }>,
  },
};

export function getAppInstance({
  data,
  appInstanceId,
  verbose = DEFAULT_VERBOSE,
}: {
  data: mixed,
  appInstanceId: string,
  verbose: boolean,
}) {
  return dispatch => {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { ENTITY_MANAGEMENT_API_URL, authId, secret, stdParameters } = apiConfig;
    // TODO URL being passed in should be custom to entitiyMangement?
    const op = new EntityManagementProvider(ENTITY_MANAGEMENT_API_URL, authId, secret);
    return dispatch({
      type: GET_APP_INSTANCE.base,
      payload: op.getAppInstance(appInstanceId, data, stdParameters, verbose),
    });
  };
}

export function updateUserLocation({
  locationData,
  appInstanceId,
  verbose = DEFAULT_VERBOSE,
}: {
  locationData: mixed,
  appInstanceId: string,
  verbose: boolean,
}) {
  return dispatch => {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { ENTITY_MANAGEMENT_API_URL, authId, secret, stdParameters } = apiConfig;
    const op = new EntityManagementProvider(ENTITY_MANAGEMENT_API_URL, authId, secret);
    return dispatch({
      type: UPDATE_LOCATION.base,
      payload: op.updateUserLocation(appInstanceId, locationData, stdParameters, verbose),
    });
  };
}
export function updateCustomSettings({
  appInstanceId,
  key,
  value,
  verbose = DEFAULT_VERBOSE,
}: {
  appInstanceId: String,
  key: String,
  value: mixed,
  verbose: boolean,
}) {
  return dispatch => {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { ENTITY_MANAGEMENT_API_URL, authId, secret, stdParameters } = apiConfig;
    const op = new EntityManagementProvider(ENTITY_MANAGEMENT_API_URL, authId, secret);
    return dispatch({
      type: UPDATE_CUSTOM_USER_SETTINGS.base,
      payload: op.updateCustomSettings(appInstanceId, key, value, stdParameters, verbose),
    });
  };
}
