// @flow

import { dataLayer } from '@weatherbug/common';

import {
  getStandardApiConfig,
  getMapApiConfig,
  type StandardApiConfigT,
} from '../../common/config/getStandardApiConfig';

import {
  getAppId,
  getAppVersion,
  getSuggestedAppInstanceId,
} from '../../modules/main/AppRegistration';
import { AppStateManager } from '../../common/utilities/appStateManager';

const DEFAULT_VERBOSE = true;

export type GetAlertsByLatLonPayload = {
  alertList: Array<mixed>, // []
  location: string, // "38.8702,-77.0823",
  locationType: 'latitudelongitude', // "latitudelongitude"
  responseTime: number, // 1555598726521
};

export function getAlertsByLatLon({
  latitude,
  longitude,
  verbose = DEFAULT_VERBOSE,
  refresh = false,
}: {
  latitude: number,
  longitude: number,
  verbose?: boolean,
  refresh?: boolean,
}) {
  return dispatch => {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { ALERTS_API_URL, authId, secret, stdParameters } = apiConfig;

    return dispatch(
      dataLayer.actions.getV1AlertsByLatLon(
        latitude,
        longitude,
        ALERTS_API_URL,
        authId,
        secret,
        stdParameters,
        verbose,
        refresh,
        getSuggestedAppInstanceId(),
        null, //emLocationId
        AppStateManager._getOs(),
        AppStateManager._getOsVersion() || 'unknown',
        getAppId(),
        getAppVersion(),
        AppStateManager._getUserLocale(),
      ),
    );
  };
}

export function getAlertsLegend(latitude: number, longitude: number) {
  return async dispatch => {
    const apiConfig = dispatch(getMapApiConfig());
    const { baseUrl: mapApiUrl, authId, secret, stdParameters } = apiConfig;
    const op = new dataLayer.providers.MapProvider(mapApiUrl, authId, secret);
    // You may or may not need this to get the defaultLayer
    // const apiResult = await op.getMapLayers(
    //   latitude,
    //   longitude,
    //   stdParameters,
    //   'english',
    //   'english',
    // );
    const defaultLayer = 'Radar.Global'; // apiResult.result.defaultLayer
    const otherResult = await op.getMapMetadata(defaultLayer, stdParameters);
    const timeslot = otherResult.ds.id;
    // width, height, latitude, longitude, timeslot, mapZoomLevel, stdParameters
    const result = await op.getAlertsLegend(
      100,
      100,
      latitude,
      longitude,
      timeslot,
      100,
      stdParameters,
    );
    return result.Result.val;
  };
}
