import React, { memo } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { Box } from '../shared/Box';

const errorStyle = {
  color: 'red',
};
const ErrorMessage = styled.div`
  color: red;
  font-size: 12px;
`;
const FormButtonContainer = styled.div`
  display: flex;
  flex: 1;
  flex-direction: row;
  justify-content: space-between;
  width: 60%;
  margin: 0 auto;
`;

const FormCancelButton = styled.button.attrs({ type: 'button' })`
  display: block;
  background: #999999;
  border-radius: 4px;
  font-size: 14px;
  line-height: 24px;
  align-items: center;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  margin-bottom: 28px;
  color: #ffffff;
  height: 36px;
  width: 98px;
`;

const FormSubmitButton = styled.button.attrs({ type: 'submit' })`
  display: block;
  background: #f8971d;
  border-radius: 4px;
  font-size: 14px;
  line-height: 24px;
  align-items: center;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  margin-bottom: 28px;
  color: #ffffff;
  height: 36px;
  width: 98px;
`;

const OtherTextbox = styled.textarea`
  height: 132px;
  margin-bottom: 2px;
  border: 1px solid #999999;
  box-sizing: border-box;
  border-radius: 4px;
  background: transparent;
  width: 418px;
`;

export const EmailTextBox = styled.input.attrs({ type: 'text' })`
  width: 418px;
  height: 40px;
  border: 1px solid #999999;
  box-sizing: border-box;
  border-radius: 4px;
  background-color: transparent;
  margin-bottom: 2px;
  padding-left: 4px;
  &::placeholder {
    padding-left: 4px;
    color: grey;
  }
`;
const FormLabel = styled.label`
  font-weight: normal;
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 18px;
  text-align: left;
`;
const TopLabel = styled.div`
  margin-bottom: 2px;
  font-weight: normal;
  font-size: 15px;
  line-height: 17px;
`;

const FormSubTitle = styled.div`
  font-weight: normal;
  font-size: 18px;
  line-height: 32px;
  display: flex;
  align-items: center;
  margin-bottom: 12px;
`;

const FormHeadder = styled.div`
  font-size: 24px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
`;

const MainFormContainer = styled.div`
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
`;
const Form = styled.form`
  color: black;
  width: 430px;
`;

const Thankyou = styled.div`
  flex-grow: 2;
  padding: 40px;
  font-size: 20px;
`;

export const FeedbackFormView = memo(
  ({
    isSubmitted,
    handleEmailChange,
    handleNameChange,
    handleMessageChange,
    handleFormSubmit,
    isValid,
    userName,
    userEmail,
    comments,
    handleCloseButton,
    errorMessage,
  }) => {
    if (!isSubmitted) {
      return (
        <Form onSubmit={handleFormSubmit}>
          <MainFormContainer>
            <FormHeadder>Contact Us</FormHeadder>
            <FormSubTitle>Tell us about your experience</FormSubTitle>
            <FormSubTitle>
              <span style={errorStyle}>*</span> Indicates a required field
            </FormSubTitle>
            <FormLabel>
              <TopLabel>
                Your Name<span style={errorStyle}>*</span>
              </TopLabel>
              <EmailTextBox onChange={handleNameChange} placeholder="Your Name" name="userName" />
              {!isValid && !userName && <ErrorMessage>Name is required</ErrorMessage>}
            </FormLabel>
            <FormLabel>
              <TopLabel>
                Your Email<span style={errorStyle}>*</span>
              </TopLabel>
              <EmailTextBox
                onChange={handleEmailChange}
                placeholder="Your-email@example.com"
                name="email"
              />
              {!isValid && !userEmail && <ErrorMessage>{errorMessage}</ErrorMessage>}
            </FormLabel>
            <FormLabel>
              <TopLabel>
                Details<span style={errorStyle}>*</span>
              </TopLabel>
              <OtherTextbox
                onChange={handleMessageChange}
                placeholder="What would you like to say?"
                name="details"
              />
              {!isValid && !comments && <ErrorMessage>Comments are required</ErrorMessage>}
            </FormLabel>
          </MainFormContainer>
          <FormButtonContainer>
            <FormSubmitButton>Submit</FormSubmitButton>
            <FormCancelButton onClick={handleCloseButton}>Cancel</FormCancelButton>
          </FormButtonContainer>
        </Form>
      );
    }
    return (
      <Box padding={1} display="flex" justifyContent="center" height={250}>
        <Box display="flex" flexDirection="column" height="100%" margin-top={50}>
          <Thankyou>
            Thank you for submitting your information we will get back to you very soon!
          </Thankyou>
          <Box flexGrow={1} margin="0 auto">
            <FormCancelButton style={{ margin: '0 auto' }} onClick={handleCloseButton}>
              Close
            </FormCancelButton>
          </Box>
        </Box>
      </Box>
    );
  },
);

FeedbackFormView.displayName = 'UninstallFormView';
FeedbackFormView.propTypes = {
  isSubmitted: PropTypes.bool,
  handleEmailChange: PropTypes.func,
  handleNameChange: PropTypes.func,
  handleMessageChange: PropTypes.func,
  handleFormSubmit: PropTypes.func,
  isValid: PropTypes.bool,
  userName: PropTypes.string,
  userEmail: PropTypes.string,
  comments: PropTypes.string,
  handleCloseButton: PropTypes.func,
  errorMessage: PropTypes.string,
};
