// @flow
import {
  LOCATION_SEARCH_BY_SEARCH_STRING,
  LOCATION_SEARCH_STARTED,
  LOCATION_SEARCH_TEXT_CHANGED,
  LOCATION_SEARCH_ENDED,
  SEARCH_LOCATION_SELECTED,
} from '../actions/locationSearchActions';

// TODO move to @weatherbug/common

type ReduxAction = Object;

export type State = {
  locationSearchIsQuerying: boolean,
  locationSearchEnabled: boolean,
  isUserTyping: boolean,
  selectedLocation: ?string,
  searchText: ?string,
  locationSearchResults: Array<mixed>,
};

const initialState: State = {
  locationSearchIsQuerying: false,
  locationSearchEnabled: false,
  isUserTyping: false,
  selectedLocation: undefined,
  searchText: undefined,
  locationSearchResults: [],
};

export function locationSearch(state: State = initialState, action: ReduxAction): State {
  switch (action.type) {
    case LOCATION_SEARCH_BY_SEARCH_STRING.pending: {
      return {
        ...state,
        locationSearchIsQuerying: true,
      };
    }
    case LOCATION_SEARCH_BY_SEARCH_STRING.fulfilled: {
      return {
        ...state,
        locationSearchResults: action.payload,
        lastError: {},
        locationSearchIsQuerying: false,
      };
    }
    case LOCATION_SEARCH_BY_SEARCH_STRING.rejected: {
      return {
        ...state,
        locationSearchResults: [],
        lastError: action.payload,
        locationSearchIsQuerying: false,
      };
    }
    case LOCATION_SEARCH_STARTED: {
      return {
        ...state,
        locationSearchResults: [],
        locationSearchEnabled: true,
      };
    }
    case LOCATION_SEARCH_TEXT_CHANGED: {
      return {
        ...state,
        isUserTyping: true,
        searchText: action.payload,
      };
    }
    case LOCATION_SEARCH_ENDED: {
      return {
        ...state,
        searchText: state.selectedLocation,
        locationSearchEnabled: false,
      };
    }
    case SEARCH_LOCATION_SELECTED: {
      let name = action.payload.CityName;
      if (action.payload.StateCode) {
        name += `, ${action.payload.StateCode}`;
      } else if (action.payload.Country) {
        name += `, ${action.payload.Country}`;
      }
      return {
        ...state,
        searchText: name,
        selectedLocation: name,
        locationSearchResults: [],
        isUserTyping: false,
      };
    }
    default: {
      return state;
    }
  }
}
