// @flow

import React, { Fragment } from 'react';
import styled, { css } from 'styled-components';
import OutsideClickHandler from 'react-outside-click-handler';

import { Popover, PopoverPane } from './Popover';

type Props = {
  icon: React$Node,
  placeholder: string,
  isSearching: boolean,
  onChangeSearchTerm: (searchTerm: string) => void,
  searchResults: Array<string>,
  onSelectResult: (result: string, index: number) => void,
  onChangeFocus: (isFocused: boolean) => void,
};

type State = {
  localText: string,
  isComponentFocused: boolean,
  highlightResultIndex: number,
};
export class SearchBar extends React.Component<Props, State> {
  input: ?HTMLInputElement;

  constructor(props: Props) {
    super(props);

    this.state = {
      localText: '',
      isComponentFocused: false,
      highlightResultIndex: 0,
    };
  }

  onChangeSearchTerm = (e: SyntheticEvent<HTMLInputElement>) => {
    const text = e.currentTarget.value;
    this.setState({ localText: text, highlightResultIndex: 0 });
    this.props.onChangeSearchTerm(text);
  };

  selectResult = (result: string, index: number) => {
    this.props.onSelectResult(result, index);
    this.setState({
      localText: '',
      isComponentFocused: false,
    });
    this.props.onChangeSearchTerm('');
  };

  setHighlightIndex = (newHighlightIndex: number) => {
    const { highlightResultIndex } = this.state;
    if (newHighlightIndex !== highlightResultIndex) {
      const result = this.props.searchResults[newHighlightIndex];
      this.setState({
        highlightResultIndex: newHighlightIndex,
        localText: result,
      });
    }
  };

  focusNextResult = () => {
    this.setHighlightIndex(
      Math.min(this.state.highlightResultIndex + 1, this.props.searchResults.length - 1),
    );
  };

  focusPrevResult = () => {
    this.setHighlightIndex(Math.max(this.state.highlightResultIndex - 1, 0));
  };

  handleKeyDown = (e: KeyboardEvent) => {
    if (e.keyCode === 9) {
      // tab
      e.preventDefault();
      if (e.shiftKey) {
        this.focusPrevResult();
      } else {
        this.focusNextResult();
      }
    } else if (e.keyCode === 38) {
      // up arrow
      e.preventDefault();
      this.focusPrevResult();
    } else if (e.keyCode === 40) {
      // down arrow
      e.preventDefault();
      this.focusNextResult();
    } else if (e.key === 'Enter') {
      e.preventDefault();
      const result = this.props.searchResults[this.state.highlightResultIndex];
      this.selectResult(result, this.state.highlightResultIndex);
      if (this.input) {
        this.input.blur();
      }
    }
  };

  render() {
    const { placeholder, searchResults, isSearching } = this.props;
    const { localText, isComponentFocused, highlightResultIndex } = this.state;
    const popoverHasContent = isSearching || searchResults.length > 0;
    return (
      <OutsideClickHandler
        onOutsideClick={() => {
          this.props.onChangeFocus(false);
          this.setState({
            isComponentFocused: false,
          });
          // Clear search after outside click?
          this.setState({ localText: '' });
          this.props.onChangeSearchTerm('');
        }}
      >
        <Container>
          <Popover
            isOpen={isComponentFocused && popoverHasContent}
            offset={{ bottom: 7 }}
            anchorElement={
              <AnchorContainer>
                <StyledInput
                  ref={r => {
                    this.input = r;
                  }}
                  type="text"
                  placeholder={placeholder}
                  value={localText}
                  onChange={this.onChangeSearchTerm}
                  onFocus={() => {
                    this.setState({ isComponentFocused: true });
                    this.props.onChangeFocus(true);
                  }}
                  onKeyDown={this.handleKeyDown}
                />
                <IconContainer>{this.props.icon}</IconContainer>
              </AnchorContainer>
            }
          >
            {/* {locationSearchIsQuerying && (
              <Box display="flex" alignItems="center" padding={2}>
                Searching...
              </Box>
            )} */}
            {(searchResults || []).map((result, index) => {
              return (
                <Fragment key={result + index}>
                  {index !== 0 && <RowDivider />}
                  <SearchResult
                    isHighlighted={highlightResultIndex === index}
                    onClick={() => {
                      this.selectResult(result, index);
                    }}
                  >
                    {result}
                  </SearchResult>
                </Fragment>
              );
            })}
          </Popover>
        </Container>
      </OutsideClickHandler>
    );
  }
}

const Container = styled.div`
  position: relative;
  ${PopoverPane} {
    width: 100%;
  }
`;

const IconContainer = styled.div`
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 30px;
`;

const AnchorContainer = styled.div`
  position: relative;
`;

const SearchResult = styled.div`
  color: rgb(100, 100, 100);
  font-weight: 500;
  padding: 16px 8px;
  padding-left: 16px;
  background: white;
  transition: background 300ms ease;

  &:hover {
    cursor: pointer;
    background: rgb(245, 245, 245);
  }

  ${props =>
    props.isHighlighted &&
    css`
      color: #2e98e3;
      box-shadow: inset 3px 0px 0px 0px #2e98e3;
    `}
`;

const RowDivider = styled.div`
  margin-left: 16px;
  height: 1px;
  background-color: rgb(230, 230, 230);
`;

export const StyledInput = styled.input`
  padding: 8px;
  padding-right: 30px; // space for icon
  border: 0.5px solid #888888;
  background-color: #ebebeb;
  border-radius: 2px;
  width: 300px;
  font-size: 16px;
  color: #888888;
`;
