// @flow

import { baseClasses, dataLayer } from '@weatherbug/common';

import {
  getStandardApiConfig,
  type StandardApiConfigT,
} from '../../common/config/getStandardApiConfig';

const { AsyncAction } = baseClasses;
export const GET_MY_LOCATION_BY_IP = new AsyncAction('GET_MY_LOCATION_BY_IP');

const DEFAULT_VERBOSE = true;

type float = number;

export type UserLocationByIpPayload = {
  aqiId: string, // "DC_Metropolitan Washington"
  cityId: string, // "US51A0022"
  cityName: string, // "Arlington"
  confirmed: string, // "true"
  countryIso2: string, // "US"
  countryIso3: string, // "USA"
  county_Municipality: string, // "Arlington County"
  dma: string, //"511"
  fipsCountry: string, //"US"
  forecastCityId: string, // "US51A0022"
  forecastZoneId: string, // "VA054"
  latitude: float,
  locationTag: mixed | null,
  longitude: float,
  pollenId: string, // "22211"
  populationSort: number, //5000
  postalCode: string, // "22204"
  responseTime: number,
  schemaVersion: 1,
  slugName: string, // "arlington-va-22204"
  stateCode: string, // "VA"
  state_Province: string, // "VA"
  timeZoneId: string, // "America/New_York"
  uvId: string, // "WASHINGTON_DC"
};

export function getUsersLocation({
  verbose = DEFAULT_VERBOSE,
  refresh = false,
}: {
  verbose?: boolean,
  refresh?: boolean,
}) {
  return dispatch => {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { LOCATIONS_API_URL, authId, secret, stdParameters } = apiConfig;
    return dispatch({
      type: GET_MY_LOCATION_BY_IP.base,
      payload: getUserLocationByIP(
        LOCATIONS_API_URL,
        authId,
        secret,
        stdParameters,
        verbose,
        refresh,
      ),
    });
  };
}

async function getUserLocationByIP(
  baseUrl,
  authId,
  secret,
  stdParameters,
  verbose,
  refresh,
): Promise<UserLocationByIpPayload> {
  const op = new dataLayer.providers.LocationSearchProvider(baseUrl, authId, secret);
  return await op.getUserLocationByIP(stdParameters, verbose, refresh);
}

export function searchLocationBySearchString({
  searchText,
  verbose = DEFAULT_VERBOSE,
  refresh = false,
}: {
  searchText: string,
  refresh?: boolean,
  verbose?: boolean,
}) {
  return dispatch => {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { LOCATIONS_API_URL, authId, secret, stdParameters } = apiConfig;
    return dispatch(
      internalSearchLocationBySearchString(
        searchText,
        LOCATIONS_API_URL,
        authId,
        secret,
        stdParameters,
        verbose,
        refresh,
      ),
    );
  };
}

// TODO move this to @weatherbug/common

export const LOCATION_SEARCH_BY_SEARCH_STRING = new AsyncAction('LOCATION_SEARCH_BY_SEARCH_STRING');

export function internalSearchLocationBySearchString(
  searchString: string,
  baseUrl: string,
  authId: string,
  secret: string,
  stdParameters: Object,
  verbose: boolean,
  refresh: boolean = false,
) {
  return {
    type: LOCATION_SEARCH_BY_SEARCH_STRING.base,
    payload: searchLocation(searchString, baseUrl, authId, secret, stdParameters, verbose, refresh),
  };
}

async function searchLocation(
  searchString: string,
  baseUrl: string,
  authId: string,
  secret: string,
  stdParameters: Object,
  verbose: boolean,
  refresh: boolean,
) {
  const op = new dataLayer.providers.LocationSearchProvider(baseUrl, authId, secret);
  return await op.searchLocation(searchString, stdParameters, verbose, refresh);
}

export const LOCATION_SEARCH_STARTED = 'LOCATION_SEARCH_STARTED';
export const LOCATION_SEARCH_TEXT_CHANGED = 'LOCATION_SEARCH_TEXT_CHANGED';
export const LOCATION_SEARCH_ENDED = 'LOCATION_SEARCH_ENDED';
export const SEARCH_LOCATION_SELECTED = 'SEARCH_LOCATION_SELECTED';

export const locationSearchStarted = () => ({
  type: LOCATION_SEARCH_STARTED,
});

export const locationSearchTextChanged = (searchText: string) => ({
  type: LOCATION_SEARCH_TEXT_CHANGED,
  payload: searchText,
});

export const locationSearchEnded = () => ({
  type: LOCATION_SEARCH_ENDED,
});

export const searchLocationSelected = (location: mixed) => ({
  type: SEARCH_LOCATION_SELECTED,
  payload: location,
});

