import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import _ from 'lodash';

import { HourlyWeatherColumn } from './HourlyWeatherColumn';
import { DayColumn } from './DayColumn';
import { HourlyItemBox } from './HourlyItemBox';
import { getHourlyForecastByLatLon } from '../../common/actions/forecastAction';

import type { HourEntry } from './HourlyWeatherColumn';

import { Title3 } from '../shared/Typography';
import { Carousel } from '../shared/Carousel';

type Props = {
  match: Match,
  history: History,
  location: Location,
  loading: boolean,
  getHourlyForecastByLatLon: typeof getHourlyForecastByLatLon,
  hourlyEntries: Array<HourEntry>,
};

const HourlyViewBare = (props: Props) => {
  const { hourlyEntries } = props;
  return (
    <Carousel header={<Title3>HOURLY</Title3>}>
      {hourlyEntries.length === 0 &&
        _.times(10, i => {
          return <HourlyItemBox key={i} />;
        })}
      {hourlyEntries.map(hourEntry => {
        const date = new Date(hourEntry.forecastDateLocalStr);
        const rawHourNum = date.getHours();
        return (
          <Fragment key={hourEntry.forecastDateLocalStr}>
            {rawHourNum === 0 && <DayColumn date={date} />}
            <HourlyWeatherColumn hourEntry={hourEntry} />
          </Fragment>
        );
      })}
    </Carousel>
  );
};

const select = store => {
  const { forecast } = store;
  const hourlyForecast = (forecast && forecast.hourly) || undefined;

  const hourlyEntries = ((hourlyForecast && hourlyForecast.hourlyForecastPeriod) || []).map(
    hourlyEntry => {
      if (!hourlyEntry) {
        return null;
      }
      return {
        iconCode: hourlyEntry.iconCode,
        temperature: hourlyEntry.temperature,
        forecastDateLocalStr: hourlyEntry.forecastDateLocalStr,
        precipProbability: hourlyEntry.precipProbability,
        relativeHumidity: hourlyEntry.relativeHumidity,
        windSpeed: hourlyEntry.windSpeed,
        windDirectionDegrees: hourlyEntry.windDirectionDegrees,
        description: hourlyEntry.description,
      };
    },
  );
  return {
    hourlyEntries,
  };
};

function mapActions(dispatch) {
  return bindActionCreators(
    {
      getHourlyForecastByLatLon,
    },
    dispatch,
  );
}

export const HourlyView = connect(
  select,
  mapActions,
)(HourlyViewBare);
