import { Component } from 'react';

const nAgt = window.navigator.userAgent;

//These regular expressions and the getOs/getOsVersion functions need to stay in sync with the legal.js file as well as UserFilterAttribute.cs
const clientStrings = [
  { s: 'windows 10', r: /(Windows 10.0|Windows NT 10.0)/i },
  { s: 'windows 8.1', r: /(Windows 8.1|Windows NT 6.3)/i },
  { s: 'windows 8', r: /(Windows 8|Windows NT 6.2)/i },
  { s: 'windows 7', r: /(Windows 7|Windows NT 6.1)/i },
  { s: 'windows NT6', r: /Windows NT 6.0/i },
  { s: 'windows NT5.2', r: /Windows NT 5.2/i },
  { s: 'windows NT5.1', r: /(Windows NT 5.1|Windows XP)/i },
  { s: 'windows NT5.0', r: /(Windows NT 5.0|Windows 2000)/i },
  { s: 'windows 9x', r: /(Win 9x 4.90|Windows ME)/i },
  { s: 'windows 98', r: /(Windows 98|Win98)/i },
  { s: 'windows 95', r: /(Windows 95|Win95|Windows_95)/i },
  { s: 'windows NT4', r: /(Windows NT 4.0|WinNT4.0|WinNT|Windows NT)/i },
  { s: 'windows CE', r: /Windows CE/i },
  { s: 'windows 16', r: /Win16/i },
  { s: 'android', r: /Android/i },
  { s: 'Open BSD', r: /OpenBSD/i },
  { s: 'Sun OS', r: /SunOS/i },
  { s: 'Linux', r: /(Linux|X11)/i },
  { s: 'ios', r: /(iPhone|iPad|iPod)/i },
  { s: 'macos', r: /Mac OS X/i },
  { s: 'macos', r: /(MacPPC|MacIntel|Mac_PowerPC|Macintosh)/i },
  { s: 'QNX', r: /QNX/i },
  { s: 'UNIX', r: /UNIX/i },
  { s: 'BeOS', r: /BeOS/i },
  { s: 'OS/2', r: /OS\/2/i },
  {
    s: 'Search Bot',
    r: /(nuhk|Googlebot|Yammybot|Openbot|Slurp|MSNBot|Ask Jeeves\/Teoma|ia_archiver)/i,
  },
];

const macosRegEx = new RegExp(/Mac OS X (10[._\d]+)/i);
const androidRegEx = new RegExp(/Android ([._\d]+)/i);
const iosRegEx = new RegExp(/OS (\d+)_(\d+)_?(\d+)?/i);

export class AppStateManager extends Component {
  static displayName = 'AppStateManager';

  render() {
    return null;
  }

  static _getOs() {
    try {
      for (const id in clientStrings) {
        let cs = clientStrings[id];
        if (cs.r.test(nAgt)) {
          if (/windows/i.test(cs.s)) {
            return 'windows';
          } else {
            return cs.s.toLowerCase();
          }
        }
      }
    } catch (error) {
      console.error(error);
    }
  }

  static _getOsVersion(os) {
    let osVersion;

    try {
      switch (os) {
        case 'windows':
          for (let id in clientStrings) {
            let cs = clientStrings[id];
            if (cs.r.test(nAgt)) {
              osVersion = cs.s.split(' ')[1];
              break;
            }
          }
          break;

        case 'macos':
          osVersion = macosRegEx.exec(nAgt)[1];
          break;

        case 'android':
          osVersion = androidRegEx.exec(nAgt)[1];
          break;

        case 'ios':
          osVersion = iosRegEx.exec(nAgt);
          osVersion = osVersion[1] + '.' + osVersion[2] + '.' + (osVersion[3] | 0);
          break;

        case 'linux':
        case 'search bot':
          osVersion = '0';
          break;

        default:
          os = 'other';
          osVersion = '0';
          break;
      }
    } catch (error) {
      console.error(error);
    }

    return osVersion;
  }

  static _getUserLocale() {
    return (window.WB && window.WB.locale) || 'en-us';
  }

  static _getLanguageCodeFromLocale(locale) {
    let localeParts = locale.split('-', 2);
    return localeParts[0];
  }

  static _getCountryCodeFromLocale(locale) {
    let localeParts = locale.split('-', 2);
    return localeParts[1];
  }
}
