/* @flow */
import { dataLayer } from '@weatherbug/common';

import { getStandardApiConfigFromStore } from '../../common/config/getStandardApiConfig';
import { AppStateManager } from '../../common/utilities/appStateManager';
import {
  getAppId,
  getAppVersion,
  getSuggestedAppInstanceId,
} from '../../modules/main/AppRegistration';

import type { ReduxState } from '../reducers/index.js';

export function logEvent(eventType: string, data?: Object) {
  return async (dispatch, getStore: () => ReduxState) => {
    const store = getStore();
    const appInstanceId = await getSuggestedAppInstanceId(store);
    const { LOGGING_API_URL, authId, secret } = getStandardApiConfigFromStore(store);

    dataLayer.actions.logEvent(
      LOGGING_API_URL,
      authId,
      secret,
      eventType,
      { deviceid: appInstanceId, schemaVersion: '1.0.0' },
      data || {},
      {
        // additional metadata here
        schemaVersion: '1.0.0',
        os: AppStateManager._getOs(),
        appVersion: getAppVersion(),
        appInstanceId,
        appId: getAppId(),
        osVersion: AppStateManager._getOsVersion() || 'unknown',
        formFactor: 'website',
      },
    );
  };
}
