/* @flow */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import styled, { css } from 'styled-components';
import _ from 'lodash';

import ImageCache from './ImageCache';
import { getBackgroundImageByLatLon } from '../../common/actions/backgroundImageActions';

type Props = {
  backgroundUrl?: string,
  latitude: number,
  longitude: number,
  getBackgroundImageByLatLon: typeof getBackgroundImageByLatLon,
  children: React$Node,
  withOverlay: boolean,
  onBackgroundImageUrlLoaded: Function,
};
type State = {
  hasLoadedMap: {
    [key: string]: boolean,
  },
};

class _WBBackgroundImage extends Component<Props, State> {
  state = {
    hasLoadedMap: {},
  };

  componentDidUpdate(prevProps) {
    const { latitude, longitude } = this.props;
    if (prevProps.latitude !== latitude || prevProps.longitude !== longitude) {
      this.maybeLoadBackgroundByLatLong(latitude, longitude);
    }
  }

  async componentDidMount() {
    this.maybeLoadBackgroundByLatLong(this.props.latitude, this.props.longitude);
  }

  async maybeLoadBackgroundByLatLong(latitude: number, longitude: number) {
    if (typeof latitude === 'number' && typeof longitude === 'number') {
      const response = await this.props.getBackgroundImageByLatLon({
        latitude,
        longitude,
      });
      const backgroundUrl = getImageUrlFromEntries(response.value.u || []);
      this.props.onBackgroundImageUrlLoaded(backgroundUrl);
      if (ImageCache.has(backgroundUrl)) {
        this.setState({ hasLoadedMap: { [backgroundUrl]: true } });
      } else {
        ImageCache.add(backgroundUrl, () => {
          this.setState({ hasLoadedMap: { [backgroundUrl]: true } });
        });
      }
    }
  }

  render() {
    const { children, backgroundUrl, withOverlay } = this.props;
    const { hasLoadedMap } = this.state;
    const imageHasLoaded = hasLoadedMap[backgroundUrl || ''];
    return (
      <Container>
        <TransitionGroup component={null}>
          {imageHasLoaded && (
            <CSSTransition key={backgroundUrl} timeout={350} classNames="item">
              <ImageView backgroundUrl={backgroundUrl} />
            </CSSTransition>
          )}
        </TransitionGroup>
        {withOverlay && <Overlay />}
        <ContentContainer>{children}</ContentContainer>
      </Container>
    );
  }
}

const Container = styled.div`
  position: relative;
  height: 100vh;
`;

const Overlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.2);
`;

const ContentContainer = styled.div`
  position: relative;
  z-index: 1;
`;

const ImageView = styled.div`
  position: fixed;
  z-index: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #2f6f8c;
  background-size: cover;
  background-repeat: no-repeat;
  ${(props: Props) =>
    props.backgroundUrl &&
    css`
    background-image: url('${props.backgroundUrl}');
  `};

  &.item-enter {
    opacity: 0;
  }
  &.item-enter-active {
    opacity: 1;
    transition: opacity 350ms ease-in;
  }
  &.item-exit {
    opacity: 1;
  }
  &.item-exit-active {
    opacity: 0;
    transition: opacity 350ms ease-in;
  }
`;

const getImageUrlFromEntries = function(entries): string {
  const landscapeBackgrounds = entries.filter(entry => entry.o === 'Landscape');
  const backgroundEntry = _.find(landscapeBackgrounds, entry => entry.sid === 'AndroidLargeTablet');

  return (backgroundEntry && backgroundEntry.u) || '';
};

const select = store => {
  const { backgroundImage } = store;

  let backgroundUrl;
  if (backgroundImage && backgroundImage.images) {
    const backgroundEntries = store.backgroundImage.images.u || [];
    backgroundUrl = getImageUrlFromEntries(backgroundEntries);
  }

  return {
    backgroundUrl,
  };
};

function mapActions(dispatch) {
  return bindActionCreators(
    {
      getBackgroundImageByLatLon,
    },
    dispatch,
  );
}

export const WBBackgroundImage = connect(
  select,
  mapActions,
)(_WBBackgroundImage);
