/* @flow */

import React, { Component } from 'react';
import styled from 'styled-components';

import { WeatherIcon } from '../shared/WeatherIcon';
import { Text1, Text2, Text3 } from '../shared/Typography';
import { Box } from '../shared/Box';

import { MONTHS } from '../../common/utilities/date';
import { TemperatureLabel } from '../shared/TemperatureLabel';

type Props = {
  dayForecast: ?{
    summaryDescription: string,
    temperature: number,
    forecastDateLocalStr: string,
    iconCode: number,
  },
  nightForecast: ?{
    summaryDescription: string,
    temperature: number,
    forecastDateLocalStr: string,
    iconCode: number,
  },
};
export class DailyWeatherColumn extends Component<Props> {
  render() {
    const { dayForecast, nightForecast } = this.props;
    const date = new Date((dayForecast || nightForecast).forecastDateLocalStr);
    const dateStr = `${MONTHS[date.getMonth()]} ${date.getDate()}`;
    return (
      <DailyItemBox>
        <Box padding={1} display="flex" justifyContent="center">
          <Text2>{dateStr}</Text2>
        </Box>

        {dayForecast && (
          <DayContainer>
            <Box marginBottom={0.5}>
              <Text1>
                <TemperatureLabel value={dayForecast.temperature} />
              </Text1>
            </Box>

            <IconContainer>
              <WeatherIcon size="small" iconCode={dayForecast.iconCode}>
                {({ src }) => <IconImg src={src} />}
              </WeatherIcon>
            </IconContainer>

            <DescriptionContainer>
              <Text3 lineClamp={2}>{dayForecast.summaryDescription}</Text3>
            </DescriptionContainer>
          </DayContainer>
        )}

        <NightContainerExpander>
          <NightContainer top={dayForecast ? 0 : -3}>
            <Box marginBottom={0.5}>
              <Text1>
                {nightForecast && <TemperatureLabel value={nightForecast.temperature} />}
              </Text1>
            </Box>

            <IconContainer>
              {nightForecast && (
                <WeatherIcon size="small" iconCode={nightForecast.iconCode}>
                  {({ src }) => <IconImg src={src} />}
                </WeatherIcon>
              )}
            </IconContainer>

            <DescriptionContainer>
              {nightForecast && <Text3 lineClamp={2}>{nightForecast.summaryDescription}</Text3>}
            </DescriptionContainer>
          </NightContainer>
        </NightContainerExpander>
      </DailyItemBox>
    );
  }
}

const DailyItemBox = styled.div`
  width: 120px;
  background-color: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.1);
  flex-shrink: 0;
  height: 350px;
  display: flex;
  flex-direction: column;
`;

const DayContainer = styled.div`
  padding: 8px;
  padding-top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  height: 155px;
`;

const NightContainerExpander = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.4);
  padding: 8px;
`;

const NightContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  height: 135px;
  position: relative;
  ${props => ('top' in props ? `top: ${props.top}px;` : '')}
`;

const DescriptionContainer = styled.div`
  // this exact height allows for 2 lines of text
  // while keeping the icon images aligned
  height: 34px;
  display: flex;
  align-items: center;
`;

const IconImg = styled.img`
  width: 100%;
`;

const IconContainer = styled.div`
  width: 64px;
  /* height: 64px; */
  border-radius: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  flex-grow: 1;
`;

export const EmptyDailyColumn = function() {
  return (
    <DailyItemBox>
      <DayContainer />
      <NightContainer />
    </DailyItemBox>
  );
};
