// @flow

import * as React from 'react';
import styled, { css } from 'styled-components';

type OffsetT = {
  bottom?: number,
  top?: number,
  left?: number,
  right?: number,
};
type Props = {
  anchorElement: any,
  isOpen: boolean | null,
  width?: number,
  offset: OffsetT | null,
  children: React.Node,
  isCentered: boolean,
};
export const Popover = function (props: Props) {
  const { isOpen, anchorElement, offset, isCentered, width } = props;

  return (
    <>
      {anchorElement}
      <PopoverPane offset={offset} isOpen={isOpen} isCentered={isCentered} width={width}>
        {props.children}
      </PopoverPane>
    </>
  );
};

function getOffset(direction: 'top' | 'right', offset?: null | OffsetT) {
  if (!offset) {
    return null;
  }

  if (direction === 'top') {
    if (typeof offset.bottom === 'number') {
      return `top: calc(100% + ${offset.bottom}px);`;
    } else if (typeof offset.top === 'number') {
      return `top: calc(100% + ${offset.top}px);`;
    }
  } else if (direction === 'right') {
    if (typeof offset.right === 'number') {
      return `right: ${offset.right}px`;
    } else if (typeof offset.left === 'number') {
      return `right: ${0 - offset.left}px`;
    }
  }
}

export const PopoverPane = styled.div`
  position: absolute;
  right: 0;
  ${(props) =>
    props.isCentered
      ? css`
      top: 0
      left: 50%
      margin-left: -${props.width / 2}px
      margin-top: 100px
      width: ${props.width}px
    `
      : css`
          ${getOffset('top', props.offset) || 'right: 0;'}
          ${getOffset('right', props.offset) || 'top: 100%;'}
        `}
  background-color: white;
  z-index: 2;
  border: 1px solid rgb(220, 220, 220);
  border-radius: 2px;
  ${(props) =>
    !props.isOpen &&
    css`
      display: none;
    `}
`;
