const SET_CCPA_OPT_STATUS = 'SET_CCPA_OPT_STATUS';
const initialState: State = {
  ccpaOptStatus: undefined,
};

export default (state: State = initialState, action): State => {
  switch (action.type) {
    case SET_CCPA_OPT_STATUS: {
      return {
        ...state,
        ccpaOptStatus: action.payload,
      };
    }

    default:
      return state;
  }
};
