// @flow

const cache = {};

const add = (url: string, onLoad?: () => void) => {
  if (!cache[url]) {
    const image = new Image();
    cache[url] = image; //new Image();
    image.src = url; // This allows the image to pre-load
    if (onLoad) {
      image.addEventListener('load', onLoad, { once: true });
    }
  } else {
    onLoad && onLoad();
  }

  return cache[url];
};

const get = (url: string) => {
  return add(url);
};

const has = (url: string) => {
  return !!cache[url];
};

const ImageCache = {
  add,
  get,
  has,
  cache,
};

export default ImageCache;
