// @flow

type ReduxAction = Object;

export type State = {
  temperatureUnit: 'english' | 'metric',
};

const initialState: State = {
  temperatureUnit: 'english', // when we make this changeable, be sure to update all of the various API calls to use this instead of always using english
};

export function userConfig(state: State = initialState, action: ReduxAction): State {
  switch (action.type) {
    default: {
      return state;
    }
  }
}
