// @flow

const USER_SELECTED_LOCATION = 'USER_SELECTED_LOCATION';
const CCPA_OPT_OUT = 'CCPA_OPT_OUT';

const LocalStorage = {
  getUserSelectedLocation: () => {
    return JSON.parse(window.localStorage.getItem(USER_SELECTED_LOCATION));
  },
  setUserSelectedLocation: (location: mixed) => {
    return window.localStorage.setItem(USER_SELECTED_LOCATION, JSON.stringify(location));
  },
  getKochavaId: () => {
    return window.storage.getItem('kv_id');
  },
  getAppInstanceId: () => {
    return window.storage.getItem('appInstanceId');
  },
  setCCPAOptOutStatus: (optOut: Boolean) => {
    return window.localStorage.setItem(CCPA_OPT_OUT, JSON.stringify(optOut));
  },
  getCCPAOptOutStatus: () => {
    const optedOut = JSON.parse(window.localStorage.getItem(CCPA_OPT_OUT));
    return !!optedOut;
  },
};

export default LocalStorage;
