/* @flow */
import { baseClasses } from '@weatherbug/common';

const { PulseBaseProvider } = baseClasses;

const configCacheKey = 'config';

export default class RemoteConfigProvider extends PulseBaseProvider {
  constructor(url: string, options: Object, logger: mixed) {
    super(url, '', '', options, logger);
    this.storage.sync[configCacheKey] = this.configSyncer.bind(this);
  }

  async configSyncer(params: Object) {
    const { id, syncParams, resolve, reject } = params;
    const responseValidator = (result) => !!result;
    return await super.syncData(
      syncParams.key,
      id,
      {},
      '',
      resolve,
      reject,
      responseValidator,
      syncParams.pulseApi,
    );
  }

  async getConfig(refresh: boolean = false) {
    const syncParams = {
      refresh,
    };
    const id = JSON.stringify(syncParams);

    if (refresh) {
      super.removeFromStorage(configCacheKey, id);
    }
    return await super.loadFromStorage(configCacheKey, id, syncParams);
  }
}
