// @flow

import React from 'react';
import styled from 'styled-components';
import moment from 'moment';

import { Icon } from '../shared/Icon';

export type AlertT = {
  AlertId: string, // "NwsVtec:KLOT-LS.Y-0006.2019~ILZ014-INZ001-002-192215-_191404"
  AlertPrimaryId: string, // "NwsVtec:KLOT-LS.Y-0006.2019"
  AlertProviderId: string, // "NwsVtec"
  AlertSecondaryId: string, // "ILZ014-INZ001-002-192215-_191404"
  AlertType: string, // "LS.Y"
  AlertTypeName: string, // "Lakeshore Flood Advisory"
  ExpiredDateTimeLocalString: string, // "2019-04-20T01:00:00"
  ExpiredDateTimeUtcString: string, // "2019-04-20T06:00:00"
  IssuedDateTimeLocalString: string, // "2019-04-19T09:04:00"
  IssuedDateTimeUtcString: string, // "2019-04-19T14:04:00"
  EventIssuedDateTimeUtcString: string | null, // "2019-04-19T14:04:00"
  EventIssuedDateTimeLocalString: string | null, // "2019-04-19T09:04:00"
  EventBeginningDateTimeUtcString: string | null,
  EventBeginningDateTimeLocalString: string | null,
  EventEndingDateTimeUtcString: string | null, // "2019-04-20T06:00:00"
  EventEndingDateTimeLocalString: string | null, // "2019-04-20T01:00:00"
  ProductExpirationDateTimeUtcString: string | null, // "2019-04-19T22:15:00"
  ProductExpirationDateTimeLocalString: string | null, // "2019-04-19T17:15:00"
  Message: string,
  RawText: string,
};

type Props = {
  onClick?: () => void,
  alert: AlertT,
  theme?: ?{
    FillColor: string,
    LineColor: string,
  },
};
export const AlertCard = function(props: Props) {
  const { alert, onClick } = props;
  const startLabel = !alert.IssuedDateTimeLocalString
    ? 'In Progress'
    : moment(alert.IssuedDateTimeLocalString).format('ddd LT');
  const endLabel = !alert.ExpiredDateTimeLocalString
    ? 'TBD'
    : moment(alert.ExpiredDateTimeLocalString).format('ddd LT');
  const alertProviderAbbreviation = alert.AlertProviderId.slice(0, 3);
  const theme = props.theme || { FillColor: '#CCC', LineColor: 'transparent' };

  return (
    <Container onClick={onClick}>
      <Circle fill={theme.FillColor} stroke={theme.LineColor}>
        {alertProviderAbbreviation}
      </Circle>
      <Content>
        <Title>{alert.AlertTypeName}</Title>

        <Description>
          <span>Start: {startLabel}</span>
          <Icon name="ArrowRight" size={14} color="#AAAAAA" />
          <span>End: {endLabel}</span>
        </Description>
      </Content>
    </Container>
  );
};

const Container = styled.div`
  padding: 16px;
  display: flex;
  flex-direction: row;
`;
const Circle = styled.div`
  width: 32px;
  height: 32px;
  border: 1px solid transparent;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  text-transform: uppercase;
  align-self: center;
  color: white;
  background-color: ${props => {
    if (props.fill === '#000000') {
      // try to use the stroke color, otherwise transparent
      return props.stroke === '#000000' ? 'transparent' : props.stroke;
    } else {
      return props.fill || '#ccc';
    }
  }}
  border-color: ${props => (props.stroke === '#000000' ? 'transparent' : props.stroke || '#666')};
`;
const Content = styled.div`
  flex-grow: 1;
  margin-left: 16px;
`;
export const Title = styled.div`
  color: #666;
  font-size: 16px;
  margin-bottom: 8px;
`;
export const Description = styled.div`
  font-size: 13px;
  color: #aaaaaa;
  display: flex;
  flex-direction: row;
  align-items: center;
  ${Icon} {
    margin: 0 8px;
  }
`;
