import React, { PureComponent } from 'react';

export class TaboolaAd extends PureComponent {
  componentDidMount() {
    const { modeId, containerId, placementId, targetType, isLastSnippet, cdnsStatus } = this.props;
    window._taboola = window._taboola || [];
    window._taboola.push({
      mode: modeId,
      container: containerId,
      placement: placementId,
      target_type: targetType,
      cdns: cdnsStatus,
    });

    if (isLastSnippet) {
      window._taboola = window._taboola || [];
      window._taboola.push({
        flush: true,
      });
    }
  }

  render() {
    const { divId, adWidth, adHeight } = this.props;
    let adStyle = { margin: 'auto', maxWidth: '1200px' };
    if (adWidth) {
      adStyle = { width: adWidth, height: adHeight, margin: 'auto' };
    }
    return <div id={divId} style={adStyle} />;
  }
}
