import React, { Component } from 'react';
import matchMedia from 'matchmediaquery';
import PropTypes from 'prop-types';

export class Map extends Component {
  static propTypes = {
    latitude: PropTypes.number.isRequired,
    longitude: PropTypes.number.isRequired,
    zoom: PropTypes.number,
    mapboxAccessToken: PropTypes.string.isRequired,
    initialLongitude: PropTypes.number,
    initialLatitude: PropTypes.number,
    initialZoom: PropTypes.number,
    enableDragRotate: PropTypes.bool,
    enablePan: PropTypes.bool,
    enableZoom: PropTypes.bool,
    showFullscreenControl: PropTypes.bool,
    showNavigationControl: PropTypes.bool,
    showCenterMarker: PropTypes.bool,
    centerMarkerText: PropTypes.string,
    centerMarkerCircleColor: PropTypes.string,
    centerMarkerTextColor: PropTypes.string,
    centerMarkerLatitude: PropTypes.number,
    centerMarkerLongitude: PropTypes.number,
    smallPinImageUrl: PropTypes.string,
    bigPinImageUrl: PropTypes.string,
    height: PropTypes.number,
    extension: PropTypes.func,
    onZoomChanged: PropTypes.func,
    onPanned: PropTypes.func,
  };

  constructor(props) {
    super(props);
    const { latitude, longitude } = this.props;
    this.state = {
      latitude,
      longitude,
    };

    this.mapZoomInPressed = this.mapZoomInPressed.bind(this);
    this.mapZoomOutPressed = this.mapZoomOutPressed.bind(this);
    this.mapPanToMarkerPressed = this.mapPanToMarkerPressed.bind(this);
    this.onZoomChanged = this.onZoomChanged.bind(this);
    this.changeZoom = this.changeZoom.bind(this);
    this.onPanned = this.onPanned.bind(this);
    this.updateZoomState = this.updateZoomState.bind(this);
  }

  async componentDidMount() {
    //TODO: How should we do this?
    this.mapboxgl = window.mapboxgl;
    this.mapboxgl.accessToken = this.props.mapboxAccessToken;
    this.mapboxgl.workerUrl = '/lib/mapbox-gl-csp-worker.js';
    const {
      initialLongitude,
      initialLatitude,
      initialZoom,
      centerMarkerLatitude,
      centerMarkerLongitude,
    } = this.props;
    const center =
      !isNaN(initialLongitude) && !isNaN(initialLatitude)
        ? [initialLongitude, initialLatitude]
        : null;
    const initZoom = !isNaN(initialZoom) ? initialZoom : 10;
    this.map = new this.mapboxgl.Map({
      container: this.mapContainer,
      center,
      zoom: initZoom,
      minZoom: 2,
      maxZoom: 15,
      style: 'mapbox://styles/mapbox/streets-v9',
      touchZoomRotate: this.props.enableZoom || false,
      scrollZoom: this.props.enableZoom,
      logoPosition: 'bottom-left',
      attributionControl: true,
      logoControl: true,
    });

    // HACK: This fixes a bug on the website where when the full screen map container loads initially the map doesn't know how much space to take so it is mostly grey.
    setTimeout(() => {
      this.map.resize();
    }, 500);

    this.onMapZoomListener = this.map.on('zoom', this.updateZoomState);
    this.map.on('dragend', this.onPanned);
    this.map.on('zoomend', this.onZoomChanged);
    const { latitude, longitude, zoom, showCenterMarker } = this.props;
    this.tryLocationUpdate(latitude, longitude, zoom, showCenterMarker);

    if (!this.props.enableDragRotate) {
      this.map.dragRotate.disable();
    }

    if (!this.props.enablePan) {
      this.map.dragPan.disable();
    }

    if (!this.props.enableZoom) {
      this.map.scrollZoom.disable();
      this.map.boxZoom.disable();
      this.map.doubleClickZoom.disable();
      this.map.touchZoomRotate.disable();
    }

    if (this.props.showFullscreenControl) {
      this.addFullscreenControl();
    }

    if (this.props.showNavigationControl) {
      this.navigationControl = new this.mapboxgl.NavigationControl();
      this.map.addControl(this.navigationControl, 'bottom-right');
    }

    if (
      this.props.showCenterMarker &&
      ((!isNaN(latitude) && !isNaN(longitude)) ||
        (!isNaN(centerMarkerLatitude) && !isNaN(centerMarkerLongitude)))
    ) {
      const markerLat = centerMarkerLatitude || latitude;
      const markerLon = centerMarkerLongitude || longitude;
      this.createMarker(
        markerLat,
        markerLon,
        this.props.centerMarkerText,
        this.props.centerMarkerCircleColor,
        this.props.centerMarkerTextColor,
      );
    }
  }

  updateZoomState(event) {
    const newZoom = Math.round(event.target._easeOptions.zoom);
    if (this.state.zoom !== newZoom) {
      this.setState({ zoom: newZoom });
    }
  }

  onZoomChanged() {
    if (this.props.onZoomChanged) {
      const zoom = this.map.getZoom();
      this.props.onZoomChanged(zoom);
    }
  }

  onPanned() {
    if (this.props.onPanned) {
      const { lat, lng } = this.map.getCenter();
      this.props.onPanned(lat, lng);
    }
  }

  componentWillUnmount() {
    if (this.map) {
      this.map.remove();
    }
  }

  async componentDidUpdate(prevProps) {
    const nextLatitude = this.props.latitude;
    const nextLongitude = this.props.longitude;
    const nextZoom = this.props.zoom;
    const currentZoom = this.state.zoom;

    const locationChanged =
      (!isNaN(nextLatitude) && this.state.latitude !== nextLatitude) ||
      (!isNaN(nextLongitude) && this.state.longitude !== nextLongitude);

    if (locationChanged || (nextZoom && currentZoom !== nextZoom)) {
      this.tryLocationUpdate(nextLatitude, nextLongitude, nextZoom, this.props.showCenterMarker);
    }

    if (this.state.enableDragRotate !== this.props.enableDragRotate) {
      this.setState({ enableDragRotate: this.props.enableDragRotate });
      if (this.props.enableDragRotate) {
        this.map.dragRotate.enable();
      } else {
        this.map.dragRotate.disable();
      }
    }

    if (this.state.enablePan !== this.props.enablePan) {
      this.setState({ enablePan: this.props.enablePan });
      if (this.props.enablePan) {
        this.map.dragPan.enable();
      } else {
        this.map.dragPan.disable();
      }
    }

    if (this.state.enableZoom !== this.props.enableZoom) {
      this.setState({ enableZoom: this.props.enableZoom });
      if (this.props.enableZoom) {
        this.map.scrollZoom.enable();
        this.map.boxZoom.enable();
        this.map.doubleClickZoom.enable();
        this.map.touchZoomRotate.enable();
      } else {
        this.map.scrollZoom.disable();
        this.map.boxZoom.disable();
        this.map.doubleClickZoom.disable();
        this.map.touchZoomRotate.disable();
      }
    }

    if (this.props.showFullscreenControl !== prevProps.showFullscreenControl) {
      if (this.props.showFullscreenControl) {
        this.addFullscreenControl();
      } else {
        this.map.removeControl(this.fullscreenControl);
        this.fullscreenControl = undefined;
      }
    }

    if (this.props.showNavigationControl !== prevProps.showNavigationControl) {
      if (this.props.showNavigationControl) {
        this.navigationControl = new this.mapboxgl.NavigationControl();
        this.map.addControl(this.navigationControl, 'bottom-right');
      } else {
        this.map.removeControl(this.navigationControl);
        this.navigationControl = undefined;
      }
    }

    if (this.props.showCenterMarker !== prevProps.showCenterMarker) {
      if (
        this.props.showCenterMarker &&
        ((!isNaN(nextLatitude) && !isNaN(nextLongitude)) ||
          (!isNaN(this.props.centerMarkerLatitude) && !isNaN(this.props.centerMarkerLongitude)))
      ) {
        const markerLat = this.props.centerMarkerLatitude || prevProps.centerMarkerLatitude;
        const markerLon = this.props.centerMarkerLongitude || prevProps.centerMarkerLongitude;

        this.createMarker(
          markerLat,
          markerLon,
          this.props.centerMarkerText,
          this.props.centerMarkerCircleColor,
          this.props.centerMarkerTextColor,
        );
      } else {
        if (this.centerMarker) {
          this.centerMarker.remove();
        }
        this.centerMarkerSpan = undefined;
      }
    }

    if (
      this.props.centerMarkerCircleColor !== prevProps.centerMarkerCircleColor &&
      this.centerMarkerSpan
    ) {
      this.centerMarkerSpan.style.border = `1px ${this.props.centerMarkerCircleColor} solid`;
    }

    if (this.props.centerMarkerText !== prevProps.centerMarkerText && this.centerMarkerSpan) {
      this.centerMarkerSpan.innerText = this.props.centerMarkerText;
    }

    if (
      this.props.centerMarkerTextColor !== prevProps.centerMarkerTextColor &&
      this.centerMarkerSpan
    ) {
      this.centerMarkerSpan.style.color = this.props.centerMarkerTextColor;
    }
  }

  addFullscreenControl() {
    this.fullscreenControl = new this.mapboxgl.FullscreenControl();
    window.fullscreenControl = this.fullscreenControl;
    window.resizeTime = 100;

    //Override the fullscreen button click to trigger a resize to fix the safari fullscreen button issue.
    const originalOnClickFullScreen = this.fullscreenControl._onClickFullscreen;
    this.fullscreenControl._onClickFullscreen = () => {
      originalOnClickFullScreen();
      setTimeout(() => {
        this.map.resize();
      }, window.resizeTime);
    };

    this.map.addControl(this.fullscreenControl, 'bottom-right');
  }

  tryLocationUpdate(latitude, longitude, zoom, showCenterMarker) {
    if (latitude && !isNaN(latitude) && longitude && !isNaN(longitude)) {
      this.setState({
        latitude,
        longitude,
        zoom,
      });

      if (this.map) {
        if (showCenterMarker) {
          this.createMarker(
            this.props.centerMarkerLatitude || latitude,
            this.props.centerMarkerLongitude || longitude,
            this.props.centerMarkerText,
            this.props.centerMarkerCircleColor,
            this.props.centerMarkerTextColor,
          );
        }
        const flyToParams = { center: [longitude, latitude] };
        if (zoom && !isNaN(zoom)) {
          flyToParams.zoom = zoom;
        }
        this.map.flyTo(flyToParams);
      }
    }
  }

  createMarker(latitude, longitude, markerText, circleColor, markerTextColor) {
    if (this.centerMarker) {
      this.centerMarker.remove();
      this.centerMarkerSpan = undefined;
    }
    var el = document.createElement('div');
    el.className = 'marker';
    //TODO: Add support for other marker sources
    let imageSrc, width, height;

    if (matchMedia('(max-width: 480px)').matches) {
      imageSrc = this.props.smallPinImageUrl;
      width = '24px';
      height = '34px';
    } else {
      imageSrc = this.props.bigPinImageUrl;
      width = '37px';
      height = '51px';
    }
    const img = document.createElement('img');
    img.src = imageSrc;
    img.style.width = width;
    img.style.height = height;
    el.appendChild(img);

    if (markerText) {
      this.centerMarkerSpan = document.createElement('span');
      this.centerMarkerSpan.innerText = markerText;
      this.centerMarkerSpan.style.position = 'absolute';

      if (markerTextColor) {
        this.centerMarkerSpan.style.color = markerTextColor;
      }

      if (circleColor) {
        this.centerMarkerSpan.style.border = `1px ${circleColor} solid`;
        this.centerMarkerSpan.style.borderRadius = '100%';
        this.centerMarkerSpan.style.textAlign = 'center';
      }

      if (matchMedia('(max-width: 480px)').matches) {
        this.centerMarkerSpan.style.top = '1px';
        this.centerMarkerSpan.style.left = '2px';
        this.centerMarkerSpan.style.padding = '0px';
        this.centerMarkerSpan.style.paddingLeft = '3px';
        this.centerMarkerSpan.style.paddingRight = '3px';
        this.centerMarkerSpan.style.fontSize = '11px';
        this.centerMarkerSpan.style.minWidth = '12px';
      } else {
        this.centerMarkerSpan.style.top = '3px';
        this.centerMarkerSpan.style.left = '3px';
        this.centerMarkerSpan.style.padding = '5px';
        this.centerMarkerSpan.style.fontSize = '16px';
        this.centerMarkerSpan.style.minWidth = '17px';
      }

      el.appendChild(this.centerMarkerSpan);
    }

    // add marker to map
    this.centerMarker = new this.mapboxgl.Marker(el)
      .setLngLat([longitude, latitude])
      .addTo(this.map);
  }

  changeZoom(zoom) {
    if (this.state.zoom !== zoom) {
      this.setState({ zoom });
      this.map.zoomTo(zoom);
    }
  }

  mapPanToMarkerPressed() {
    if (this.centerMarker) {
      const { lng, lat } = this.centerMarker.getLngLat();
      this.map.easeTo({ center: [lng, lat] });
    } else {
      const { latitude, longitude } = this.props;
      this.map.easeTo({ center: [longitude, latitude] });
    }
  }

  mapZoomInPressed() {
    if (this.map) {
      this.map.zoomIn();
    }
  }

  mapZoomOutPressed() {
    if (this.map) {
      this.map.zoomOut();
    }
  }

  render() {
    const { extension } = this.props;
    const { latitude, longitude, zoom } = this.state;
    return (
      <div style={{ ...styles.container, height: this.props.height }}>
        <div style={styles.mapStyle} ref={el => (this.mapContainer = el)} />
        {extension &&
          this.map &&
          extension(
            this.map,
            latitude,
            longitude,
            this.mapZoomOutPressed,
            this.mapZoomInPressed,
            this.mapPanToMarkerPressed,
            zoom,
            this.changeZoom,
          )}
      </div>
    );
  }
}

const styles = {
  container: {
    width: '100%',
    background: '#F9F9F9',
    border: '1px solid #EEEEEE',
    borderRadius: '2px',
    display: 'flex',
    position: 'relative',
  },
  mapStyle: {
    height: '100%',
    width: '100%',
  },
};
