// @flow

import React from 'react';
import styled from 'styled-components';

import { Text3 } from '../shared/Typography';

const RowItem = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 4px 0;
`;

export const Table = styled.div`
  width: 100%;
  align-self: stretch;
  ${RowItem}:last-child {
    margin-bottom: 0;
  }
`;

type Props = {
  label: string,
  value: string,
};
export const TableRow = ({ label, value }: Props) => {
  return (
    <RowItem>
      <Text3>{label}</Text3>
      <Text3 emphasize>{value}</Text3>
    </RowItem>
  );
};
