// @flow

import { dataLayer } from '@weatherbug/common';

import {
  getStandardApiConfig,
  type StandardApiConfigT,
} from '../../common/config/getStandardApiConfig';

const DEFAULT_VERBOSE = true;

type Datestring = string;
type float = number;

export type DailyPayload = {
  dailyForecastPeriods: Array<{
    detailedDescription: string,
    forecastDateLocalStr: Datestring,
    forecastDateUtcStr: Datestring,
    iconCode: number,
    isNightTimePeriod: boolean,
    summaryDescription: string,
    temperature: number,
  }>,
  forecastCreatedUtcStr: Datestring,
  location: string, //"US11W0001",
  locationType: 'city' | string,
  responseTime: number,
};

export function getDailyForecastByLatLon({
  latitude,
  longitude,
  offset = 0,
  units = 'english',
  verbose = DEFAULT_VERBOSE,
  refresh = false,
}: {
  latitude: number,
  longitude: number,
  offset?: number,
  units?: 'english' | 'metric',
  verbose?: boolean,
  refresh?: boolean,
}) {
  return dispatch => {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { FORECAST_API_URL, authId, secret, stdParameters } = apiConfig;
    return dispatch(
      dataLayer.actions.getDailyForecastByLatLon(
        `${latitude},${longitude}`,
        'LatitudeLongitude',
        offset,
        units,
        FORECAST_API_URL,
        authId,
        secret,
        stdParameters,
        verbose,
        refresh,
      ),
    );
  };
}

export type HourlyForecastEntry = {
  adjustedPrecipProbability: number,
  cloudCoverPercent: number,
  description: null | string,
  dewPoint: number,
  feelsLike: number,
  forecastDateLocalStr: Datestring,
  forecastDateUtcStr: Datestring,
  globeTemperature: number,
  iconCode: number,
  precipCode: number,
  precipProbability: number,
  precipRate: float,
  relativeHumidity: number,
  snowRate: number,
  surfacePressure: float,
  temperature: number,
  thunderstormProbability: number,
  wetBulbTemperature: number,
  windDirectionDegrees: number,
  windSpeed: number,
};

export type HourlyPayload = {
  forecastCreatedUtcStr: Datestring,
  hourlyForecastPeriod: Array<HourlyForecastEntry>,
  location: string, // e.g. "US11W0001",
  locationType: 'city' | string,
  responseTime: number,
};

export function getHourlyForecastByLatLon({
  latitude,
  longitude,
  offset = 0,
  units = 'english',
  verbose = DEFAULT_VERBOSE,
  refresh = false,
}: {
  latitude: number,
  longitude: number,
  offset?: number,
  units?: 'english' | 'metric',
  verbose?: boolean,
  refresh?: boolean,
}) {
  return dispatch => {
    const apiConfig: StandardApiConfigT = dispatch(getStandardApiConfig());
    const { FORECAST_API_URL, authId, secret, stdParameters } = apiConfig;
    return dispatch(
      dataLayer.actions.getHourlyForecastByLatLon(
        `${latitude},${longitude}`,
        'LatitudeLongitude',
        offset,
        units,
        FORECAST_API_URL,
        authId,
        secret,
        stdParameters,
        verbose,
        refresh,
      ),
    );
  };
}
