/* @flow */

import { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import { getAppInstance } from '../../common/actions/entityManagementActions';
import { getUsersLocation } from '../../common/actions/locationSearchActions';
import { AppStateManager } from '../../common/utilities/appStateManager';
import LocalStorage from '../shared/LocalStorage';

import type { GetAppInstanceResponseT } from '../../common/actions/entityManagementActions';
import type { ReduxState } from '../../common/reducers/index.js';

type Props = {
  store: ReduxState,
};

// returns an uppercase hex token formatted into groups of 8-4-4-4-12
function getRandomToken() {
  // E.g. 8 * 32 = 256 bits token
  let randomPool = new Uint8Array(32);
  window.crypto.getRandomValues(randomPool);
  let hex = '';
  for (let i = 0; i < randomPool.length; ++i) {
    hex += randomPool[i].toString(16);
  }
  // hex === e.g. db18458e2782b2b77e36769c569e263a53885a9944dd0a861e5064eac16f1a
  return `${hex.slice(0, 8)}-${hex.slice(8, 12)}-${hex.slice(12, 16)}-${hex.slice(
    16,
    20,
  )}-${hex.slice(20, 32)}`.toUpperCase();
}

export async function getSuggestedAppInstanceId(store: ReduxState): Promise<string> {
  const appInstanceId = store && store.entityManagement && store.entityManagement.appInstanceId;
  if (appInstanceId) {
    return appInstanceId;
  }

  const storedAppInstanceId = await LocalStorage.getAppInstanceId();
  if (storedAppInstanceId) {
    return storedAppInstanceId;
  }

  const newAppInstanceId = getRandomToken();
  await setSuggestedAppInstanceId(newAppInstanceId);
  return newAppInstanceId;
}

async function setSuggestedAppInstanceId(appInstanceId: string): Promise<void> {
  await window.storage.setItem('appInstanceId', appInstanceId);
}

export function getAppId(): null | string {
  if (!window || !window.ui || !window.ui.browser) {
    return null;
  }
  return `com.weatherbug.browser.extension.${window.ui.browser.toLowerCase()}`; // manifest if different manifests per build?  or ENV if using build:firefox and build:chrome?
}

export function getAppVersion(): 'unknown' | string {
  const manifest =
    (window.chrome &&
      window.chrome.runtime &&
      window.chrome.runtime.getManifest &&
      window.chrome.runtime.getManifest()) ||
    undefined;

  if (manifest === undefined) {
    console.warn(
      'Manifest unavailable; this only works when running as an extension.  Defaulting appVersion to "unknown"',
    );
  }
  const appVersion: string = (manifest && manifest.version) || 'unknown';
  return appVersion;
}

const MAX_ATTEMPTS = 8;

class _AppRegistration extends Component<Props> {
  componentDidMount() {
    this.registerApp();
  }

  registerApp(attemptNum: number = 1, isKochavaIdRequired?: boolean = false) {
    console.debug(`registerApp${attemptNum > 1 ? ` (Attempt ${attemptNum})` : ''}`);
    this.attemptToRegister(isKochavaIdRequired)
      .then(success => {
        if (!success) {
          console.info('Failed to completely register');
          if (attemptNum > MAX_ATTEMPTS) {
            console.warn(`Failed to completely registerApp after ${MAX_ATTEMPTS} attempts`);
            return;
          }
          setTimeout(() => {
            this.registerApp(attemptNum + 1, true);
          }, 50 * Math.pow(2, attemptNum - 1));
        }
      })
      .catch(err => {
        console.error('error in attemptToRegist(): ', err);
      });
  }

  async attemptToRegister(isKochavaIdRequired?: boolean = false): Promise<boolean> {
    const kochavaId = await LocalStorage.getKochavaId();
    if (!kochavaId) {
      console.warn('kochavaId not available');
    }

    const appVersion: string = getAppVersion();

    const appId = getAppId();
    const cultureInfo: string = AppStateManager._getUserLocale(); // e.g. "en-us"
    const osType: string = AppStateManager._getOs(); // e.g. "windows"
    const osVersion: string = AppStateManager._getOsVersion() || 'unknown';

    const vendors = kochavaId ? [{ name: 'kochava', id: kochavaId }] : [];

    const installCountry: ?string = await this.props
      .getUsersLocation({ refresh: false })
      .then(response => {
        if (!response) {
          return undefined;
        }

        return (response && response.value && response.value.countryIso2) || undefined;
      });

    const suggestedAppInstanceId = await getSuggestedAppInstanceId(this.props.store);

    const appInstanceInfo = {
      app: {
        id: appId,
        version: appVersion,
      },
      cultureInfo,
      installGuid: suggestedAppInstanceId,
      os: {
        type: osType,
        version: osVersion,
      },
      pushToken: '0000000000000000000000000000000000000000000000000000000011111111', // hard coded string,
      installCountry,
      vendors,
    };

    return this.props
      .getAppInstance({
        data: appInstanceInfo,
        appInstanceId: appInstanceInfo.installGuid,
      })

      .then((response: { value: GetAppInstanceResponseT }) => {
        const actualAppInstanceId = response.value.appInstanceId;
        window.kochava.identify({
          appInstanceId: actualAppInstanceId,
        });

        if (suggestedAppInstanceId !== actualAppInstanceId) {
          setSuggestedAppInstanceId(actualAppInstanceId);
        }
        return kochavaId ? true : false;
      });
  }

  render() {
    return null;
  }
}

function mapState(store) {
  return {
    store,
  };
}

function mapActions(dispatch) {
  return bindActionCreators(
    {
      getAppInstance,
      getUsersLocation,
    },
    dispatch,
  );
}

export const AppRegistration = connect(
  mapState,
  mapActions,
)(_AppRegistration);
